import React, {useEffect, useState} from 'react';
import {useDispatch, useSelector} from "react-redux";
import * as actionTypes from "../../../redux/actionTypes";

const Modal = ({ name, image, title, isVideo, subclass, children }) => {
    const dispatch = useDispatch();
    const openedModalName = useSelector(state => state.mainReducer.modalName);

    const [opacity, setOpacity] = useState('0');

    useEffect(() => {
        if (!!openedModalName && openedModalName === name) {
            setTimeout(() => setOpacity('1'), 10);
        }
    }, [openedModalName, name]);

    const hideModal = () => {
        setOpacity('0');

        setTimeout(() => dispatch({ type: actionTypes.CLEAR_MODAL_NAME }), 200);
    };

    return !!name && name === openedModalName && <div
        className={ `modal opened${isVideo ? ' modalVideo' : ''}${ subclass ? ' ' + subclass : '' }` }
        style={{ opacity }}
    >
        <div className="before" onClick={ hideModal } />

        <div className="modal__inner">
            <button className="modal__close icon icon-close" onClick={ hideModal } />

            { !!image && <div className="modal__image">
                <img src={ image } alt={ !!title && title } />
            </div> }

            <div className="modal__body">
                { !!title && <p className="modal__title">{ title }</p> }
                { children }
            </div>
        </div>
    </div>;
};

export default Modal;
