import React from 'react';

const Checkbox = ({ name, returnedValue, isChecked, isDisabled, onChange, children, subclass }) => {

    const handleOnChange = (event) => {
        onChange(event.target.checked, returnedValue);
    };

    return <label
        className={`checkbox${subclass ? ' ' + subclass : ''}`}
    >
        <input
            type="checkbox"
            name={ name }
            checked={ isChecked }
            disabled={ isDisabled }
            onChange={ handleOnChange }
        />

        <span className="checkbox__inner">
            <span className="dot" />

            <span className="text">{ children }</span>
        </span>
    </label>;
};

export default Checkbox;
