import React from 'react';
import PlayButton from "../playButton/playButton";
import ModalPortal from "../../portals/modalPortal/modalPortal";
import Modal from "../modal/modal";
import {useDispatch, useSelector} from "react-redux";
import * as actionTypes from "../../../redux/actionTypes";
import Player from "../player/player";
import {NavLink} from "react-router-dom";

const VideoElement = props => {

    const dispatch = useDispatch();
    const openedModalName = useSelector(state => state.mainReducer.modalName);

    const { id, name, img, duration, lessons, tests, price, difficulty, description, previewVideoUrl, isPurchased, category, link } = props.data;

    const nameForModal = 'video_' + props.category + '_' + id;

    let priceText = price === 0 ? 'Бесплатно' : price.toLocaleString() + ' руб.';
    if (isPurchased) {
        priceText = 'Приобретено'
    }

    let difficultyText;
    switch (difficulty) {
        case 'newbies': difficultyText = 'Начинающим'; break;
        case 'pro': difficultyText = 'Профессионалам'; break;
        default: difficultyText = '';
    }

    const showModal = name => {
        dispatch({ type: actionTypes.SET_MODAL_NAME, params: { name } });
    };

    // -----------------------------

    return <div className="video">
        <div className="video__inner">
            <div className="video__img">
                <img src={ img } alt=""/>
            </div>

            <p className="video__name">{ name }</p>

            <div className="video__info">
                {duration ?
                    <span
                        className="video__date"
                    >Длительность: {duration}</span> :

                    <p className="video__paramsTop">
                        {lessons ?
                            <span className="element">
                                <span className="icon icon-file"/>
                                <span className="text">{ lessons } занятий</span>
                            </span> : null}

                        {tests ?
                            <span className="element">
                                <span className="icon icon-planchette"/>
                                <span className="text">{ tests } теста</span>
                            </span> : null}
                    </p>
                }

                <div className="separator"/>

                <p className="video__params">
                    <span
                        className={`price${price === 0 ? ' free' : ''}${isPurchased ? ' purchased' : ''}`}
                    >{ priceText }</span>
                    <span className="divider"> | </span>
                    <span className="difficulty">{ difficultyText }</span>
                </p>
            </div>

            <div className="video__buttons">
                <div className="video__play">
                    <PlayButton onClick={() => showModal(nameForModal)}>Быстрый проссмотр</PlayButton>
                </div>

                <div className="video__open">
                    { category && category === 'courses' ?
                        isPurchased ?
                            <NavLink
                                to={`/blog/course-${id}/`}
                                className="video__openBtn"
                            >Открыть курс</NavLink>
                            :
                            <a
                                className="video__openBtn"
                                href={ link }
                                target="_blank"
                                rel="noreferrer"
                            >Купить</a>
                        :
                        <a
                            className="video__openBtn"
                            href={ link }
                            target="_blank"
                            rel="noreferrer"
                        >Перейти</a>
                    }
                </div>
            </div>
        </div>

        { !!openedModalName && openedModalName === nameForModal && <ModalPortal>
            <Modal name={ nameForModal }>
                <Player
                    title={ name }
                    description={ description }
                    url={ previewVideoUrl }
                    isAutoplay={ true }
                />
            </Modal>
        </ModalPortal> }
    </div>;
};

export default VideoElement;
