import React from "react";
import {statusType} from "../../../../types";

const LessonContent = ({ lesson, index, icon }) => {
    return <>
        <span className={`iconLeft icon ${icon}`}/>

        <span className="description">
                                Занятие {index + 1}
            {lesson.status === statusType.disabled ? ' | Будет доступно после прохождения занятия ' + index : null}
                            </span>

        <span className="title">{lesson.name}</span>

        { lesson.hasTest &&
            <span className="hasTest">+ Тест</span>
        }

        { lesson.status !== statusType.disabled &&
            <span className="iconRight icon icon-arr_rounded_right"/>
        }
    </>;
};

export default LessonContent;
