import React, {useEffect} from 'react';
import {useDispatch, useSelector} from "react-redux";
import {mainPageActions} from "../../redux/actions/mainPageActions";

import Filter from "../../components/blocks/filter/filter";
import CoursesList from "../../components/blocks/coursesList/coursesList.js";

import './mainpage.sass';

const MainPage = () => {
    const dispatch = useDispatch();

    const mainPageReducer = useSelector(state => state.mainPageReducer);
    const categories = mainPageReducer.appSections;
    const filterList = mainPageReducer.filterList;
    const coursesList = mainPageReducer.coursesList;

    useEffect(() => {
        // updateFilter(setDefaultFilter());
    }, []);

    /**
     * Проверка отображения конкретного курса при активности
     * соответствующего пункта в фильтре
     * @param data
     * @returns {boolean}
     */
    const checkFilter = (data) => {
        let results = [];
        let result = false;

        filterList.forEach(elem => {
            if (elem.isActive) {
                if ((elem.name === 'newbies' || elem.name === 'pro') && data.difficulty === elem.name) {
                    results.push(true);
                } else if (elem.name === 'free' && data.price === 0) {
                    results.push(true);
                } else if (elem.name === 'popular' && data.isPopular) {
                    results.push(true);
                } else {
                    results.push(false);
                }
            }
        });

        results.map(elem => result = result || elem);

        return result;
    };

    const updateFilter = array => {
        dispatch(mainPageActions.updateFilter(array));
    };

    /**
     * Метод для фильтрации курсов по категории и покупкам.
     * Возвращает список курсов отдельной категории либо только купленные курсы
     * @param categoryName - имя категории, по которому производится фильтрация
     * @returns {T[]}
     */
    const getCoursesList = categoryName => {
        return coursesList.filter(elem =>  (categoryName === 'purchases' ? elem.isPurchased : elem.category === categoryName) && checkFilter(elem));
    };

    // -----------------------------

    return <div className="mainpage">
        <div className="container">
            { filterList &&
                <Filter
                    list={ filterList }
                    updateFilter={ updateFilter }
                />
            }

            { categories && categories.map((category, key) =>
                <CoursesList
                    key={ key }
                    category={ category }
                    list={ getCoursesList(category.code) }
                />
            )}
        </div>
    </div>;
};

export default MainPage;
