import React, {useEffect, useState} from 'react';
import {useSelector} from "react-redux";
import {statusType} from "../../../types";
import LoaderBlock from "../../../components/common/loaderBlock/loaderBlock";

import './coursePanel.sass';

const CoursePanel = () => {
    const course = useSelector(state => state.courseReducer.course);
    const isCourseLoaded = useSelector(state => state.courseReducer.isCourseLoaded);

    const [lessonsAll, updateLessonsAll] = useState(0);
    const [lessonsCount, updateLessonsCount] = useState(0);
    const [lessonsFinishedCount, updateFinishedLessonsCount] = useState(0);
    const [testsCount, testsLessonsCount] = useState(0);
    const [testsFinishedCount, updateFinishedTestsCount] = useState(0);

    useEffect(() => {
        if (isCourseLoaded) {
            courseProgress();
        }
    }, [isCourseLoaded, course]); // eslint-disable-line react-hooks/exhaustive-deps

    const courseProgress = () => {
        const list = course.lessons;

        let lessons = course.lessons.length,
            finishedLessons = 0,
            tests = 0,
            finishedTests = 0;

        if (list) {
            list.forEach(lesson => {
                if (lesson.status === statusType.finished) {
                    finishedLessons++;
                }

                if (lesson.hasTest) {
                    tests++;
                }

                if (lesson.testStatus === statusType.finished) {
                    finishedTests++;
                }
            });

            updateLessonsAll(list.length);
        }

        updateLessonsCount(lessons);
        updateFinishedLessonsCount(finishedLessons);
        testsLessonsCount(tests);
        updateFinishedTestsCount(finishedTests);
    };

    return <div className={`coursePanel ${!isCourseLoaded ? 'loaded' : ''}`}>
        { isCourseLoaded ?
            <>
            {course.lessons ?
                <>
                    <div className="coursePanel__progress">
                        <div className="progressPersents">

                            <span className="label">Выполнено:</span>

                            <div className="progressPersents__line">
                                <div
                                    className="progressPersents__lineInner"
                                    style={{'width': (lessonsFinishedCount * 100) / lessonsAll + '%'}}
                                />
                            </div>

                            <div className="progressPersents__number">
                                {Math.floor((lessonsFinishedCount * 100) / lessonsAll) + '%'}
                            </div>
                        </div>

                        <div className="progressElement">
                            <span className="label">Уроки:</span>
                            <span className="value">{lessonsFinishedCount}/{lessonsCount}</span>
                        </div>

                        <div className="progressElement">
                            <span className="label">Тесты:</span>
                            <span className="value">{testsFinishedCount}/{testsCount}</span>
                        </div>
                    </div>

                    <div className="coursePanel__buttons">
                        <button className="coursePanel__button buttonOnline">
                            <span className="icon icon-camera"/>
                            <span className="text">Онлайн встреча</span>
                        </button>

                        <button className="coursePanel__button buttonInstructions">
                            <span className="icon icon-play_square"/>
                            <span className="text">Инструкция</span>
                        </button>
                    </div>
                </>
                :
                <p>:(</p>
            } </>
            :
            <>
                <LoaderBlock />
                <LoaderBlock />
                <LoaderBlock />
                <LoaderBlock />
                <LoaderBlock />
            </>
        }

    </div>;
};

export default CoursePanel;
