import * as actionTypes from "../actionTypes";
import {UPDATE_LESSON_STATUS, UPDATE_TEST_SCORES} from "../actionTypes";

export const courseActions = {
    /**
     * Говорим приложению, что курс в данный момент загружается
     * @returns {{type: string}}
     */
    setCourseNotLoaded: () => ({
        type: actionTypes.SET_COURSE_NOT_LOADED
    }),

    /**
     * Говорим приложению, что загрузка курса завершена
     * @returns {{type: string}}
     */
    setCourseLoaded: () => ({
        type: actionTypes.SET_COURSE_LOADED
    }),

    /**
     * Говорим приложению, что урок в данный момент загружается
     * @returns {{type: string}}
     */
    setLessonNotLoaded: () => ({
        type: actionTypes.SET_LESSON_NOT_LOADED
    }),

    /**
     * Говорим приложению, что загрузка урока завершена
     * @returns {{}}
     */
    setLessonLoaded: () => ({
        type: actionTypes.SET_LESSON_LOADED
    }),

    /**
     * Сохранение полученных от сервера данных учебного курса
     * @param params
     * @returns {{type: string, params}}
     */
    setCourseData: params => ({
        type: actionTypes.SET_COURSE_DATA,
        params
    }),

    /**
     * Сохранение полученных от сервера данных урока
     * @param params
     * @returns {{type: string, params}}
     */
    setLessonData: params => ({
        type: actionTypes.SET_LESSON_DATA,
        params
    }),

    /**
     * Обновление статуса урока
     * @param params
     * @returns {{type: string, params}}
     */
    updateLessonStatus: params => ({
        type: actionTypes.UPDATE_LESSON_STATUS,
        params
    }),

    /**
     * Сохранение текущего времени просмотра
     * для видео урока
     *
     * @param params
     * @returns {{type: string, params}}
     */
    saveVideoProgress: params => ({
        type: actionTypes.SAVE_VIDEO_PROGRESS,
        params
    }),

    /* TESTS */

    /**
     * Обновление флагов isChecked для ответов на вопрос в тесте
     * при выделении соответствующего ему чекбокса или радиокнопки
     * @param params
     * @returns {{type: string, params}}
     */
    updateTestAnswersStatus: params => ({
        type: actionTypes.UPDATE_TEST_ANSWERS_STATUS,
        params
    }),

    /**
     * Переход к предыдущему вопросу теста
     * @returns {{type: string, params}}
     */
    setTestPrevQuestion: () => ({
        type: actionTypes.SET_TEST_PREV_QUESTION
    }),

    /**
     * Переход к следующему вопросу теста
     * @param params
     * @returns {{type: string, params}}
     */
    setTestNextQuestion: () => ({
        type: actionTypes.SET_TEST_NEXT_QUESTION
    }),

    /**
     * Очистка результатов теста
     * @returns {{type: *, params}}
     */
    clearTest: () => ({
        type: actionTypes.CLEAR_TEST
    }),

    /**
     * Сохранение типов вопросов теста, полученных от сервера
     * @returns {{type: string}}
     */
    setTestQuestionTypes: params => ({
        type: actionTypes.SET_TEST_QUESTION_TYPES,
        params
    }),

    /**
     * Обновление статуса теста
     * @param params
     * @returns {{type: *, params}}
     */
    setTestStatus: params => ({
        type: actionTypes.SET_TEST_STATUS,
        params
    }),

    /**
     * Обновление теста после обработки данных (парсинг запускается
     * после получения данных урока от сервера)
     * @param params
     * @returns {{type: string, params}}
     */
    setParsedTestData: params => ({
        type: actionTypes.SET_PARSED_TEST_DATA,
        params
    }),

    /**
     * Обновление полученных очков за прохождение теста
     * @param params
     * @returns {{type: string, params}}
     */
    updateTestScores: params => ({
        type: actionTypes.UPDATE_TEST_SCORES,
        params
    }),

    /**
     * Сброс всех ответов пользователя на тест
     * @param params
     * @returns {{type: string, params}}
     */
    deleteAllTestAnswers: params => ({
       type: actionTypes.DELETE_ALL_TEST_ANSWERS,
       params
    })

    /* END TESTS */
}
