import React from 'react';
import VideosTableLine from "./line/line";

import './videosTable.sass';

const VideosTable = ({ list, toggleButtonText, onToggleButtonClick }) => {
    return <div className="videosTableOuter">
        <table className="videosTable" style={{ borderCollapse: 'collapse' }}>
            { list && list.map((data, index) =>
                <VideosTableLine
                    data={ data }
                    key={ index }
                />
            )}
        </table>

        <button
            className="more"
            onClick={() => onToggleButtonClick(true)}
        >
            <span className="icon icon-arr_rounded_right"/>
            <span className="text">{ toggleButtonText }</span>
        </button>
    </div>;
};

export default VideosTable;
