import React from 'react';
import PlayButton from "../../../components/common/playButton/playButton";
import ButtonMore from "../../../components/common/buttonMore/buttonMore";
import {buttonMoreType, statusType} from "../../../types";
import Player from "../../../components/common/player/player";
import {NavLink} from "react-router-dom";
import './video.scss';
import {useSelector} from "react-redux";

const LessonVideo = ({ lessons, onVideoPause, onVideoStop }) => {

    const lesson = useSelector(state => state.courseReducer.lesson);

    let time = 0;

    if (lesson.video.time && lesson.video.time.length > 0) {
        const timeArr = lesson.video.time.split(':');

        time = (+timeArr[0]) * 60 * 60 + (+timeArr[1]) * 60 + (+timeArr[2]);
    }

    const getIconClass = (status) => {
        switch (status) {
            case statusType.finished:
                return 'icon-check';

            case statusType.started:
                return 'icon-pause';

            case statusType.enabled:
                return 'icon-play';

            case statusType.disabled:
                return 'icon-lock';

            default: return 'icon-check';
        }
    };

    return <div className="container">
        <div className="lessonVideo">
            { lesson &&
                lessons.find(less=>less.id===lesson.id).status === statusType.disabled ?
                    <div className="lessonVideo__video noAccess">
                        <p className="lessonVideo__noAccess">У вас нет доступа к этому уроку. <br/>Выберите доступный урок в списке уроков</p>
                    </div>
                : lesson.video &&
                    <div className="lessonVideo__video">
                        <Player
                            url={lesson.video.url}
                            time={time}
                            playerType={'videoLesson'}
                            onPause={onVideoPause}
                            onStop={onVideoStop}
                        />
                    </div>
            }

            <div className="lessonVideo__list">
                { lessons ?
                    <ul>
                        { lessons.map((elem, index) => {
                            return <li key={ index }>
                                { elem.status !== statusType.disabled ?
                                    <NavLink
                                        to={`/blog/course-${lesson.courseId}/lesson-${elem.id}`}
                                        className={`element`}
                                    >
                                        { elem.image &&
                                            <span className="img">
                                                {/*<img src={elem.image} alt=""/>*/}
                                                <img src="https://fin-plan.org/upload/iblock/743/431_na_237.jpg" alt=""/>
                                                <PlayButton/>
                                            </span>
                                        }

                                        <span className="text">
                                            <span className="element__number">
                                                <span className={`icon ${getIconClass(elem.status)}`}/>

                                                <span className="subtitle">Занятие {index + 1}</span>
                                            </span>

                                            <span className="element__title">{elem.name}</span>
                                        </span>
                                    </NavLink>
                                    :
                                    <span className="element locked">
                                        { elem.image &&
                                            <span className="img">
                                                {/*<img src={elem.image} alt=""/>*/}
                                                <img src="https://fin-plan.org/upload/iblock/743/431_na_237.jpg" alt=""/>

                                                <PlayButton/>
                                            </span>
                                        }

                                        <span className="text">
                                            <span className="element__number">
                                                <span className={`icon ${getIconClass(elem.status)}`}/>

                                                <span className="subtitle">Занятие {index + 1}</span>
                                            </span>

                                            <span className="element__title">{elem.name}</span>
                                        </span>
                                    </span>
                                }
                            </li>;
                        })}
                    </ul>
                    :
                    <p>Список уроков пуст</p>
                }
            </div>
        </div>

        <div className="lessonVideo__listToggleOuter">
            <ButtonMore type={ buttonMoreType.button }>Список уроков</ButtonMore>
        </div>
    </div>;
};

export default LessonVideo;
