import React, {useEffect} from "react";
import {useDispatch, useSelector} from "react-redux";

import PanelFloat from "./components/blocks/panelFloat/panelFloat";

import './App.sass';
import MainRoutes from "./routes/mainRoutes";
import TopSectionRoutes from "./routes/topSectionRoutes";
import PanelRoutes from "./routes/panelRoutes";
import {mainThunks} from "./redux/thunks/mainThunks";

const App = () => {
    const dispatch = useDispatch();
    const isWindowBlocked = useSelector(state => state.mainReducer.isWindowBlocked);

    /**
     * Запуск первых запросов к серверу
     */
    useEffect(() => {
        dispatch(mainThunks.domain());
    }, [dispatch]);

    /**
     * Проверка на поле isWindowBlocked
     * Блокируется скролл страницы, если  значение равно true
     */
    useEffect(() => {
        const html = document.querySelector('html');
        const body = document.querySelector('body');

        if (isWindowBlocked) {
            html.style.overflow = 'hidden';
            body.style.overflow = 'hidden';
        } else {
            html.style.overflow = null;
            body.style.overflow = null;
        }
    }, [isWindowBlocked]);

    return (
        <div className="LearningApp">
            <div className="sectionTopOuter">
                <div className="sectionTop">
                    <div className="container">
                        <TopSectionRoutes/>
                    </div>
                </div>
            </div>

            <PanelFloat>
                <PanelRoutes/>
            </PanelFloat>

            <MainRoutes/>
        </div>
    );
};

export default App;
