import React, {useEffect, useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {statusType, testQuestionType} from "../../../../types";
import Checkbox from "../../../../components/common/checkbox/checkbox";
import Radio from "../../../../components/common/radio/radio";
import {courseActions} from "../../../../redux/actions/courseActions";
import {courseThunks} from "../../../../redux/thunks/courseThunks";
import RadioButtonsList from "../../../../components/common/radio/list";

const TestStartedContent = ({test, scoring}) => {
    const dispatch = useDispatch();

    const [isNextBtnDisabled, setNextBtnDisabled] = useState(true);
    const [radioAnswersList, setRadioAnswersList] = useState([]);

    const currentQuestion = test.questions[test.currentQuestionIndex];
    let collectedPoints = test.collectedPoints;

    useEffect(() => {
        updateRadioAnswersList();
    }, []); // eslint-disable-line react-hooks/exhaustive-deps

    useEffect(() => {
        updateRadioAnswersList();
        scoring();
    }, [test.questions]); // eslint-disable-line react-hooks/exhaustive-deps

    useEffect(() => {
        checkNextBtnDisabled();
        updateRadioAnswersList();
    }, [test.currentQuestionIndex]); // eslint-disable-line react-hooks/exhaustive-deps

    const updateRadioAnswersList = () => {
        let list = [];

        if (currentQuestion.type !== testQuestionType.multiply) {
            list = currentQuestion.answers.map((answer, index) => {
                return {
                    id: answer.id,
                    name: `testAnswers_${currentQuestion.id}`,
                    value: answer.id,
                    isChecked: answer.isChecked ? answer.isChecked : false,
                    text: (currentQuestion.type === testQuestionType.scale ? `${index + 1}` : `${answer.text}`)
                }
            });
        }

        setRadioAnswersList(list);
    };

    const changeAnswer = (event, answerId) => {
        let checkedAnswersCount = 0;

        const answers = currentQuestion.answers.map(answer => {
            if (currentQuestion.type === testQuestionType.multiply) {
                if (answer.id === answerId) {
                    answer.isChecked = event;
                }
            } else {
                if (answer.id === answerId) {
                    answer.isChecked = true;
                } else {
                    if (answer.isChecked) {
                        dispatch(courseThunks.deleteTestAnswer({
                            testId: test.id,
                            questionId: currentQuestion.id,
                            answerId: answer.id
                        }));
                    }
                    answer.isChecked = false;
                }
            }

            return answer;
        });

        checkNextBtnDisabled(checkedAnswersCount <= 0);

        if (answerId !== -1) {
            dispatch(courseThunks.updateTestAnswers({
                answers,
                testId: test.id,
                questionId: currentQuestion.id,
                answerId,
                isChecked: event
            }));
        }
    };

    const goToPrevQuestion = () => {
        dispatch(courseActions.setTestPrevQuestion());
    };

    const goToNextQuestion = () => {
        dispatch(courseActions.setTestNextQuestion());
    };

    const finishTest = () => {
        const status = collectedPoints === test.pointsToPass ? statusType.finished : statusType.finishedWithErrors;

        dispatch(courseThunks.setTestStatus(status));
    };

    const checkNextBtnDisabled = () => {
        let isDisabled = true;

        currentQuestion.answers.forEach(answer => {
            if (answer.isChecked) {
                isDisabled = false;

                return;
            }
        })

        setNextBtnDisabled(isDisabled);
    };

    return <>
        <div className="test__inner">
            <div className="testQuestion">
                <div className="testQuestion__top">
                    <h2 className="testQuestion__question">{currentQuestion.question}</h2>

                    <span className="testQuestion__points">баллов: {currentQuestion.points}</span>
                </div>

                <div className="testQuestion__content">
                    { currentQuestion.imageUrl &&
                        <div className="testQuestion__img">
                            <img src={currentQuestion.imageUrl} alt=""/>
                        </div>
                    }

                    <div
                        className={[
                            'testQuestion__answers',
                            currentQuestion.type === testQuestionType.scale && 'scale'
                        ]
                            .filter(e => !!e)
                            .join(' ')}
                    >
                        { currentQuestion.type === testQuestionType.multiply ?
                            <div className="checkboxesList">
                                { currentQuestion.answers.map((answer, index) => {
                                    return <Checkbox
                                        key={index}
                                        name={`testAnswers_${currentQuestion.id}_${index}`}
                                        returnedValue={index}
                                        isChecked={answer.isChecked || false}
                                        onChange={(event) => changeAnswer(event, answer.id)}
                                    >
                                        <span>{answer.text}</span>
                                    </Checkbox>;
                                })}
                            </div>
                            :
                            <RadioButtonsList
                                list={radioAnswersList}
                                name={`testAnswers_${currentQuestion.id}`}
                                onChange={(event, value) => changeAnswer(event, value)}
                            />
                        }

                    </div>
                </div>
            </div>
        </div>

        <div className="test__buttons">
            {test.currentQuestionIndex !== 0 && <button
                className="test__buttonPrev button button-gray"
                onClick={goToPrevQuestion}
            >Предыдущий вопрос</button>}

            <p className="test__buttonsCount">{test.currentQuestionIndex + 1} / {test.questions.length}</p>

            {test.currentQuestionIndex === test.questions.length - 1 ? <button
                className="test__buttonNext button button-orange"
                disabled={isNextBtnDisabled}
                onClick={finishTest}
            >Узнать результат</button> : <button
                className="test__buttonNext button button-orange"
                disabled={isNextBtnDisabled}
                onClick={goToNextQuestion}
            >Следующий вопрос</button>}
        </div>
    </>;
}

export default TestStartedContent;
