import React, {useEffect, useState} from 'react';
import {NavLink} from "react-router-dom";
import {useSelector} from "react-redux";
import LoaderBlock from "../../../components/common/loaderBlock/loaderBlock";

import './sectionTop.sass';

const SectionTopLesson = (props) => {
    const [lessonNumber, setLessonNumber] = useState(0);

    const lessons = useSelector(state => state.courseReducer.course.lessons);
    const lesson = useSelector(state => state.courseReducer.lesson);
    const isCourseLoaded = useSelector(state => state.courseReducer.isCourseLoaded);
    const isLessonLoaded = useSelector(state => state.courseReducer.isLessonLoaded);

    useEffect(() => {
        if (Object.keys(lesson).length > 0 && lessons && lessons.length > 0) {
            getCurrentLessonNumber();
        }
    }, [lesson, lessons]); // eslint-disable-line react-hooks/exhaustive-deps

    const getCurrentLessonNumber = () => {
        let number = lessons.findIndex(elem => elem.id === lesson.id) + 1;

        if (number) {
            setLessonNumber(number);
        }
    }

    return <div className="postDetails">
        <div className="postDetails__nav">
            <NavLink to={`/blog/course-${props.match.params.courseId}/`} className="postDetails__back more">
                <span className="icon icon-arr_rounded_left" />
                <span className="text">К списку уроков</span>
            </NavLink>
        </div>

        { isCourseLoaded && isLessonLoaded ?
            !!lessonNumber &&
                <p className="postDetails__subtitle">Занятие { lessonNumber }</p>
            :
            <LoaderBlock className="postDetails__subtitleLoader" width={ 300 } />
        }

        { isCourseLoaded && isLessonLoaded ?
            lesson &&
                <h1 className="postDetails__title">{lesson.name ? lesson.name : 'Урок не найден!'}</h1>
            :
            <LoaderBlock width={ 500 } height={ 45 } />
        }
    </div>;
};
export default SectionTopLesson;
