import React from "react";
import TestPreviewEnabled from "./enabled";
import {statusType} from "../../../../types";
import TestPreviewDisabled from "./disabled";
import TestPreviewFinished from "./finished";
import FinishedWithErrors from "./finishedWithErrors";

const TestPreview = ({ status, setTestStatus }) => {

    const getPreviewTemplate = () => {
        switch (status) {
            case statusType.disabled: {
                return <TestPreviewDisabled/>;
            }

            case statusType.enabled: {
                return <TestPreviewEnabled
                    onButtonClick={() => setTestStatus(statusType.started)}
                />;
            }

            case statusType.finished: {
                return <TestPreviewFinished/>;
            }

            case statusType.finishedWithErrors: {
                return <FinishedWithErrors/>;
            }

            default: {
                return <TestPreviewDisabled/>;
            }
        }
    };

    return <div className="test__preview">{ getPreviewTemplate() }</div>;
};

export default TestPreview;
