import React from 'react';

import './progressLine.sass';

const PlayerProgressLine = ({ progress, buffer, onSeekChange, onSeekMouseUp }) => {
    const handleSeekChange = e => onSeekChange(parseFloat(e.target.value));
    const handleSeekMouseUp = e => {
        onSeekMouseUp(parseFloat(e.target.value));
    };

    return <div className="PlayerProgressLine">
        <input
            type="range"
            min={ 0 }
            max={ 0.999999 }
            step='any'
            value={ progress }

            onMouseUp={ handleSeekMouseUp }
            onTouchEnd={ handleSeekMouseUp }
            onChange={ handleSeekChange }
        />

        <div className="PlayerProgressLine__before" />

        <div
            className="PlayerProgressLine__progress"
            style={{ width: progress * 100 + '%' }}
        />

        <div
            className="PlayerProgressLine__buffer"
            style={{ width: buffer * 100 + '%' }}
        />
    </div>
};

export default PlayerProgressLine;
