import React, {useEffect} from 'react';
import {useDispatch, useSelector} from "react-redux";
import {courseThunks} from "../../../redux/thunks/courseThunks";
import {statusType} from "../../../types";
import TestPreview from "./preview";
import TestStartedContent from "./startedContent/startedContent";
import TestFinishedContent from "./finishedContent/finishedContent";
import {courseActions} from "../../../redux/actions/courseActions";

import './test.scss';

const Test = () => {
    const dispatch = useDispatch();

    const course = useSelector(state => state.courseReducer.course);
    const lesson = useSelector(state => state.courseReducer.lesson);
    const test = useSelector(state => state.courseReducer.lesson.test);

    useEffect(() => {
        scoring();
    }, []); // eslint-disable-line react-hooks/exhaustive-deps

    const setTestStatus = status => {
        dispatch(courseThunks.setTestStatus(status));
    };

    const startTest = () => setTestStatus(statusType.started);

    const getNextLessonLink = () => {
        let nextLessonId = -1;

        let isCurrentLessonFound = false;
        course.lessons.forEach((currentLesson, index) => {
            if (currentLesson.id === lesson.id) {
                isCurrentLessonFound = true;

                nextLessonId = course.lessons[index + 1].id;
            }
        });

        return `/blog/course-${course.id}/lesson-${nextLessonId}`;
    };

    const scoring = () => {
        let points = 0;

        test.questions.forEach(q => q.answers.forEach(answer => {
            if (answer.isChecked) {
                if (answer.isRight) {
                    points += answer.points;
                } else {
                    points--;
                }
            }
        }));

        dispatch(courseActions.updateTestScores({points}));
    };

    return <div className="test">
        { test.status !== statusType.started &&
            <TestPreview
                collectedPoints={test.collectedPoints}
                pointsToPass={test.pointsToPass}
                status={test.status}
                setTestStatus={setTestStatus}
                onTestStart={startTest}
            />
        }

        { test.status === statusType.started &&
            <TestStartedContent test={test} scoring={scoring} />
        }

        { (test.status === statusType.finished || test.status === statusType.finishedWithErrors) &&
            <TestFinishedContent
                test={test}
                nextLessonLink={getNextLessonLink}
            />
        }
    </div>;
};

export default Test;
