import React, {useEffect} from "react";
import {useDispatch, useSelector} from "react-redux";

import LessonsList from "../../components/blocks/lessonsList/lessonsList";
import FilesList from "../../components/blocks/filesList/filesList";
import CourseAnswers from "./answers/answers";
import SectionArticles from "../../components/blocks/sectionArticles/sectionArticles";
import {courseThunks} from "../../redux/thunks/courseThunks";
import LoadersList from "./loadersList/loadersList";

const CoursePage = (props) => {
    const dispatch = useDispatch();

    const isCourseLoaded = useSelector(state => state.courseReducer.isCourseLoaded);
    const course = useSelector(state => state.courseReducer.course);
    const isPayedCoursesListLoaded = useSelector(state => state.mainPageReducer.isPayedCoursesListLoaded);
    const articles = useSelector(state => state.mainReducer.articles);

    useEffect(() => {
        const courseId = props.match.params.courseId;

        if (isPayedCoursesListLoaded && courseId && courseId !== course?.id) {
            dispatch(courseThunks.getCourseData(courseId))
        }
    }, [isPayedCoursesListLoaded]); // eslint-disable-line react-hooks/exhaustive-deps

    return <div className="pageCourse">
        { isCourseLoaded ?
            course.isPurchased || course.price === 0 ?
                <>
                    <div className="container">
                        { course.lessons &&
                            <LessonsList course={course}/>
                        }
                    </div>

                    { course.materials &&
                        <FilesList files={course.materials}/>
                    }

                    { course.answersVideos &&
                        <CourseAnswers list={course.answersVideos} />
                    }

                    { articles &&
                        <SectionArticles articles={articles} />
                    }
                </>
                :
                <div className="container">
                    <p>У Вас нет доступа к данному курсу</p>
                </div>
            :
            <div className="container">
                <LoadersList />
            </div>
        }
    </div>;
};

export default CoursePage;
