import * as actionTypes from "../actionTypes";
import {statusType} from "../../types";

const initialState = {
    isCourseLoaded: false,
    isLessonLoaded: false,
    isTestLoaded: false,

    /**
     * Список типов вопросов вместе с их id
     */
    testQuestionTypes: [],

    /**
     * Объект с данными по текущему загруженному курсу
     */
    course: {},

    /**
     * Объект с данными по текущему загруженному уроку
     */
    lesson: {}
};

const courseReducer = (state = initialState, action) => {
    switch (action.type) {
        case actionTypes.SET_COURSE_NOT_LOADED: {
            return {
                ...state,
                isCourseLoaded: false
            };
        }

        case actionTypes.SET_COURSE_LOADED: {
            return {
                ...state,
                isCourseLoaded: true
            };
        }

        case actionTypes.SET_LESSON_NOT_LOADED: {
            return {
                ...state,
                isLessonLoaded: false
            };
        }

        case actionTypes.SET_LESSON_LOADED: {
            return {
                ...state,
                isLessonLoaded: true
            };
        }

        case actionTypes.SET_COURSE_DATA: {
            return {
                ...state,
                course: action.params.course
            };
        }

        case actionTypes.SET_LESSON_DATA: {
            return {
                ...state,
                lesson: action.params.lesson,
                isTestLoaded: false
            };
        }

        case actionTypes.UPDATE_LESSON_STATUS: {
            return {
                ...state,
                course: {
                    ...state.course,
                    lessons: action.params.lessons
                }
            }
        }

        case actionTypes.SAVE_VIDEO_PROGRESS: {
            const setTestEnabled = () => {
                if (state.lesson.test) {
                    return {
                        ...state.lesson.test,
                        status: action.params.isFinished ? statusType.enabled : (state.lesson.test.status || statusType.disabled)
                    }
                }

                return state.lesson.test
            };

            return {
                ...state,
                lesson: {
                    ...state.lesson,
                    video: {
                        ...state.lesson.video,
                        time: action.params.time,
                        isFinished: action.params.isFinished ? action.params.isFinished : state.lesson.video.isFinished
                    },
                    test: setTestEnabled()
                }
            };
        }

        /* TESTS */

        case actionTypes.UPDATE_TEST_ANSWERS_STATUS: {
            return {
                ...state,
                lesson: {
                    ...state.lesson,
                    test: {
                        ...state.lesson.test,
                        questions: state.lesson.test.questions.map((question, index) => {
                            if (index === state.lesson.test.currentQuestionIndex) {
                                question.answers = action.params.answers;
                            }

                            return question;
                        })
                    }
                }
            };
        }

        case actionTypes.SET_TEST_PREV_QUESTION: {
            const currentIndex = state.lesson.test.currentQuestionIndex;

            return {
                ...state,
                lesson: {
                    ...state.lesson,
                    test: {
                        ...state.lesson.test,
                        currentQuestionIndex: currentIndex > 0 ? currentIndex - 1 : 0
                    }
                }
            };
        }

        case actionTypes.SET_TEST_NEXT_QUESTION: {
            const currentIndex = state.lesson.test.currentQuestionIndex;
            const questionsLength = state.lesson.test.questions.length;

            return {
                ...state,
                lesson: {
                    ...state.lesson,
                    test: {
                        ...state.lesson.test,
                        currentQuestionIndex: currentIndex < questionsLength - 1 ? currentIndex + 1 : questionsLength - 1
                    }
                },
            }
        }

        case actionTypes.CLEAR_TEST: {
            return {
                ...state,
                lesson: {
                    ...state.lesson,
                    test: {
                        ...state.lesson.test,
                        answeredQuestions: 0,
                        collectedPoints: 0,
                        questions: state.lesson.test.questions.map(question => {
                            const answers = question.answers.map(answer => {
                                answer.isChecked = false;

                                return answer;
                            });

                            question.isAnswered = false;
                            question.isPassed = false;
                            question.answers = answers;

                            return question;
                        })
                    }
                }
            }
        }

        case actionTypes.SET_TEST_STATUS: {
            return {
                ...state,
                lesson: {
                    ...state.lesson,
                    test: {
                        ...state.lesson.test,
                        status: action.params.status
                    }
                }
            }
        }

        case actionTypes.SET_TEST_QUESTION_TYPES: {
            return {
                ...state,
                testQuestionTypes: action.params.list
            }
        }

        case actionTypes.SET_PARSED_TEST_DATA: {
            return {
                ...state,
                isTestLoaded: true,
                lesson: {
                    ...state.lesson,
                    test: {
                        ...state.lesson.test,
                        collectedPoints: action.params.collectedPoints,
                        currentQuestionIndex: action.params.currentQuestionIndex,
                        questions: action.params.questions
                    }
                }
            }
        }

        case actionTypes.UPDATE_TEST_SCORES: {
            return {
                ...state,
                lesson: {
                    ...state.lesson,
                    test: {
                        ...state.lesson.test,
                        collectedPoints: action.params.points
                    }
                }
            }
        }

        case actionTypes.DELETE_ALL_TEST_ANSWERS: {
            return {
                ...state,
                lesson: {
                    ...state.lesson,
                    test: {
                        ...state.lesson.test,
                        questions: action.params.questions,
                        collectedPoints: 0,
                        currentQuestionIndex: 0
                    }
                }
            }
        }

        /* END TESTS */

        default: return state;
    }
};

export default courseReducer;
