import React, {useEffect, useState} from 'react';

const Filter = ({ list, updateFilter }) => {

    let allCount = 0;
    const [isListLoaded, setListLoaded] = useState(false);
    const [activeBtns, setActiveBtn] = useState([]);

    list.map(elem => allCount += elem.count);

    /**
     * Если список фильтров получен впервые -
     * делаем активным весь фильтр по-умолчанию
     */
    useEffect(() => {
        if (list.length > 0 && !isListLoaded) {
            setActiveBtnHandler('all');
            setListLoaded(true);
        }
    }, [list]); // eslint-disable-line react-hooks/exhaustive-deps

    /**
     * Метод добавляет элемент фильтра в список
     * активных фильтров
     * @param name
     */
    const setActiveBtnHandler = name => {
        let array = [ ...activeBtns.filter(elem => elem !== 'all') ];

        if (name === 'all') {
            array = setAllBtns();
        } else {
            array = array.filter(elem => elem !== name);
            array.push(name);
        }

        if (array.length >= list.length) {
            array = setAllBtns();
        }

        updateFilter(array);
        setActiveBtn(array);
    };

    /**
     * Метод удаляет элемент фильтра из списка
     * активных фильтров
     * @param name
     */
    const deleteActiveBtnHandler = name => {
        let array = [ ...activeBtns.filter(elem => elem !== name && elem !== 'all') ];

        if (array.length === 0) {
            array = setAllBtns();
        }

        updateFilter(array);
        setActiveBtn(array);
    };

    /**
     * Метод делает активными все элементы фильтра
     * Возвращает массив с именами элементов фильтра
     * @returns {*[]}
     */
    const setAllBtns = () => [ ...list.map(elem => elem.name), 'all' ];

    // -----------------------------

    return <div className="filter">
        <div className="filter__inner">
            <ul className="filter__list">
                <li
                    className={`filterElement all${activeBtns.includes('all') ? ' active' : ''}`}
                >
                    <button
                        className="filterElement__btn"
                        onClick={() => setActiveBtnHandler('all')}
                    >Все <span className="count">{ allCount }</span></button>
                </li>

                { list.map((elem, key) =>
                    <li
                        key={ key }
                        className={`filterElement${activeBtns.includes(elem.name) ? ' active' : ''}`}
                    >
                        <button
                            className="filterElement__btn"
                            onClick={() => setActiveBtnHandler(elem.name)}
                        >{ elem.text } <span className="count">{ elem.count }</span></button>
                        <button
                            className="filterElement__close icon icon-close"
                            onClick={() => deleteActiveBtnHandler(elem.name)}
                        />
                    </li>
                )}
            </ul>
        </div>
    </div>;
};

export default Filter;
