import React from 'react';
import {path} from "./path";
import {Route, Switch} from "react-router-dom";

const PanelRoutes = () => {
    return <Switch>
        {
            path.map((route, key) => {
                return <Route
                    key={ key }
                    exact={ !!route.isExact }
                    path={ route.path }
                    component={ route.components.panel }
                />
            })
        }
    </Switch>
};
export default PanelRoutes;
