import React, {useEffect, useRef, useState} from 'react';
import {useDispatch, useSelector} from "react-redux";

import './categoriesNav.sass';
import * as actionTypes from "../../../redux/actionTypes";
import {Link} from "react-scroll";
import {mainPageActions} from "../../../redux/actions/mainPageActions";

const CategoriesNav = () => {
    const dispatch = useDispatch();
    const categories = useSelector(state => state.mainPageReducer.appSections);

    const [hasScroll, setHasScroll] = useState(false);
    const [isCategoriesLoaded, setCategoriesLoaded] = useState(false);

    const nav = useRef(null);
    const activeElement = useRef(null);

    const scrollDuration = 500;
    let interval;

    useEffect(() => {
        hasScrollCheck();

        if (!isCategoriesLoaded) {
            if (categories.length > 0) {
                setCategoriesLoaded(true);
                setActiveCategory(categories[0].name);
            }
        }

        window.addEventListener('resize', hasScrollCheck);

        return window.removeEventListener('resize', hasScrollCheck);
    }, [categories]); // eslint-disable-line react-hooks/exhaustive-deps

    const setActiveCategory = name => {
        dispatch(mainPageActions.updateAppSection(name));
    };

    const scrollOnHover = (direction, timeout = 10) => {
        interval = setInterval(() => scrollInfinite(direction), timeout);
    };

    const scrollOnClick = direction => {
        scrollStepByStep(direction);
        clearInterval(interval);
    };

    const scrollInfinite = direction => {
        if (direction === 'right') {
            nav.current.scrollLeft += 3;
        } else {
            nav.current.scrollLeft -= 3;
        }

        return false;
    };

    const scrollStepByStep = direction => {
        const startTime = Date.now();

        const render = () => {
            const dt = Date.now() - startTime;

            if (dt < 100) {
                if (direction === 'right') {
                    nav.current.scrollLeft += 20;
                } else {
                    nav.current.scrollLeft -= 20;
                }

                requestAnimationFrame(render)
            }
        };

        requestAnimationFrame(render);
    };

    const hasScrollCheck = () => {
        setHasScroll(nav.current.scrollWidth > nav.current.offsetWidth);
    }

    const handleSetActive = event => dispatch({ type: actionTypes.UPDATE_ACTIVE_CATEGORY, params: { name: event } });

    // -----------------------------

    return <div className={`smartNav__outer tabsNav__outer ${hasScroll ? ' with__buttons' : ''}`}>
        {hasScroll && <button
            className="smartNav__prev smartNav__btn icon icon-arr_simple_left"
            onClick={() => scrollOnClick('left')}
            onMouseEnter={() => scrollOnHover('left')}
            onMouseLeave={() => clearInterval(interval)}
        />}

        <ul className="smartNav tabsNav" ref={nav}>
            { categories.map(element => {
                return (
                    <li
                        key={ element.id }
                        className={ element.isActive ? 'active' : '' }
                        ref={ element.isActive ? activeElement : null }
                        onClick={ () => setActiveCategory(element.code) }
                    >
                        <Link
                            activeClass="active"
                            to={ element.code }
                            spy={ true }
                            smooth={ true }
                            isDynamic={ true }
                            onSetActive={ handleSetActive }
                            duration={ scrollDuration }
                        >{ element.name }</Link>
                    </li>
                );
            }) }
        </ul>

        {hasScroll && <button
            className="smartNav__next smartNav__btn icon icon-arr_simple_right"
            onClick={() => scrollOnClick('right')}
            onMouseEnter={() => scrollOnHover('right')}
            onMouseLeave={() => clearInterval(interval)}
        />}
    </div> ;
};

export default CategoriesNav;
