import * as actionTypes from '../actionTypes';

const initialState = {
    /**
     * Основные разделы приложения.
     * отображаются на главной странице в плавающей панели
     */
    appSections: [],

    /**
     * Список ключей для сложности курсов
     */
    difficulty: [],

    /**
     * Список всех курсов, сервисов и вебинаров
     */
    coursesList: [],

    /**
     * Список id купленных курсов
     */
    payedCoursesList: [],
    isPayedCoursesListLoaded: false,

    /**
     * Список фильтров для курсов, сервисов и вебинаров
     */
    filterList: [],

    /**
     * Список слайдов для слайдера в верхней секции на главной странице
     */
    topSectionSlides: [],
}

const mainPageReducer = (state = initialState, action) => {
    switch (action.type) {
        case actionTypes.SET_APP_SECTIONS: {
            return {
                ...state,
                appSections: action.params.appSections
            };
        }

        case actionTypes.SET_DIFFICULTY_TYPES: {
            return {
                ...state,
                difficulty: action.params
            }
        }

        case actionTypes.UPDATE_ACTIVE_CATEGORY: {
            return {
                ...state,
                appSections: state.appSections.map(elem => {
                    elem.isActive = elem.name === action.params.name;
                    return elem;
                })
            };
        }

        case actionTypes.SET_COURSES_LIST: {
            return {
                ...state,
                coursesList: action.params.coursesList
            }
        }

        case actionTypes.SET_PAYED_COURSES_LIST: {
            return {
                ...state,
                payedCoursesList: action.params.payedCoursesList,
                isPayedCoursesListLoaded: true
            }
        }

        case actionTypes.SET_FILTER: {
            return {
                ...state,
                filterList: action.params.filterList.map(elem => {
                    elem.isActive = true;
                    return elem;
                })
            }
        }

        case actionTypes.UPDATE_FILTER: {
            return {
                ...state,
                filterList: state.filterList.map(elem => {
                    elem.isActive = action.params.includes(elem.name);

                    return elem;
                })
            };
        }

        case actionTypes.SET_TOP_SECTION_SLIDES: {
            return {
                ...state,
                topSectionSlides: action.params.topSectionSlides
            };
        }

        default:
            return state;
    }
};

export default mainPageReducer;
