import React from 'react';
import {NavLink} from "react-router-dom";
import {useSelector} from "react-redux";

import './sectionTop.sass';
import LoaderBlock from "../../../components/common/loaderBlock/loaderBlock";

const SectionTopCourse = () => {
    const course = useSelector(state => state.courseReducer.course);
    const isCourseLoaded = useSelector(state => state.courseReducer.isCourseLoaded);

    return <div className="postDetails">
        <div className="postDetails__nav">
            <NavLink to="/blog" className="postDetails__back more">
                <span className="icon icon-arr_rounded_left"/>
                <span className="text">К списку семинаров</span>
            </NavLink>
        </div>

        { isCourseLoaded ?
            <h1 className="postDetails__title">{course.name ? course.name : 'Курс не найден!'}</h1>
            :
            <LoaderBlock width={ 500 } height={ 45 } />
        }
    </div>;
};
export default SectionTopCourse;
