import React from 'react';

import './loadersList.sass';
import LoaderBlock from "../../../components/common/loaderBlock/loaderBlock";

const LoadersList = () => {
    const list = new Array(5).fill(1);

    return <div className="loadingList">
        <h2 className="titleBordered">Загружаем список занятий...</h2>

        { list && list.map((elem, index) => {
            return <div className="loadingList__element" key={ index }>
                <LoaderBlock />
                <LoaderBlock />
            </div>;
        })}
    </div>;
};

export default LoadersList;
