import React, {useEffect, useState} from 'react';
import screenfull from "screenfull";

import './controls.sass';

const PlayerControls = ({ playerWrapper, volume, isMuted, updateVolume, updateMuted }) => {

   const [volumeIcon, setVolumeIcon] = useState('volume');
   const [isFullscreen, toggleFullscreen] = useState(false);

   useEffect(() => {
      setVolumeIconHandler(volume);

      if (screenfull.isEnabled) {
         screenfull.on('change', () => {
            toggleFullscreen(screenfull.isFullscreen);
         });
      }

      return () => screenfull.off('change', () => {
         toggleFullscreen(screenfull.isFullscreen);
      });
   }, []); // eslint-disable-line react-hooks/exhaustive-deps

   useEffect(() => {
      setVolumeIconHandler(volume);
   }, [isMuted]); // eslint-disable-line react-hooks/exhaustive-deps

   const setVolumeIconHandler = (volume) => {
      if (isMuted || volume === 0) {
         setVolumeIcon('volume-x');
      } else if (volume > 0 && volume <= .3) {
         setVolumeIcon('volume');
      } else if (volume > .3 && volume <= .8) {
         setVolumeIcon('volume-1');
      } else if (volume > .8) {
         setVolumeIcon('volume-2');
      } else {
         setVolumeIcon('volume');
      }
   };

   const changeVolume = (e) => {
      const volume = parseFloat(e.target.value);

      setVolumeIconHandler(volume);

      updateVolume(volume);
   };

   const changeMuted = () => {
      updateMuted(!isMuted);
   };

   const setFullscreen = () => {
      if (screenfull.isEnabled) {
         screenfull.toggle(playerWrapper.current);
      }
   };

   return <>
       <div className="playerVolume">
          <button
              className={`playerVolume__btn icon icon-${ volumeIcon }`}
              onClick={ changeMuted }
          />

          <div className="playerVolume__inner">
             <div className={`playerVolume__line${isMuted ? ' muted' : ''}`}>
                <input
                    type='range'
                    min={ 0 }
                    max={ 1 }
                    step='any'
                    value={ volume }
                    onChange={ changeVolume }
                    onClick={ () => updateMuted(false) }
                />

                <div className="playerVolume__lineBefore" />

                <div
                    className="playerVolume__lineProgress"
                    style={{ width: volume * 100 + '%' }}
                />
             </div>
          </div>
       </div>

      <button
          className={ `playerResize icon icon-${isFullscreen ? 'minimize' : 'maximize'}` }
          onClick={ setFullscreen }
      />
   </>;
};

export default PlayerControls;
