import React from 'react';

const PlayButton = ({ children, onClick }) => {
    const handleClick = e => {
        onClick(e);
    };

    return <button
        className={ `playButton ${!children ? 'withoutText' : ''}` }
        onClick={ handleClick }
    >
        <span className="icon icon-play"/>

        { children &&
            <span className="text">{ children }</span>
        }
    </button>;
};

export default PlayButton;
