import React from "react";
import {useDispatch} from "react-redux";
import {statusType} from "../../../../types";
import {courseThunks} from "../../../../redux/thunks/courseThunks";
import {NavLink} from "react-router-dom";

const TestFinishedContent = ({test, nextLessonLink}) => {
    const dispatch = useDispatch();

    const restartTest = () => {
        dispatch(courseThunks.restartTest());
    };

    return <>
        <p className="test__finishedScoresSubtitle">Набранные баллы:</p>

        <p className="test__finishedScores">{test.collectedPoints} из {test.pointsToPass}</p>

        { test.status === statusType.finishedWithErrors &&
            <p className="test__finishedText">При ответе на вопросы вы допустили ошибки. <br/>Вам нужно пройти тест
                заново чтобы получить доступ к следующему занятию или связаться с менеджером в чате</p>
        }

        { test.status === statusType.finished &&
            <>
                <p className="test__finishedText">Поздравляем! Вы успешно прошли тестирование. <br/>Теперь вы можете
                    приступить к следующему уроку. Удачи!</p>


                { test.questions.map((question, index) => {
                    return <div
                        className="test__inner"
                        key={index}
                    >
                        { test.questions &&
                            <div className="testQuestion">
                                <div className="testQuestion__top">
                                    <h2 className="testQuestion__question">{question.question}</h2>

                                    <span className="testQuestion__points">баллов: {question.points}</span>
                                </div>

                                <div className="testQuestion__content">
                                    { question.imageUrl &&
                                        <div className="testQuestion__img">
                                            <img src={question.imageUrl} alt=""/>
                                        </div>
                                    }

                                    { question.answers.map((answer, index) => {
                                        if (answer.isRight && answer.isChecked) {
                                            return <p
                                                className="testQuestion__finishAnswer"
                                                key={index}
                                            >
                                                <span className="icon icon-check"/>

                                                <span className="text">{answer.text}</span>
                                            </p>
                                        }

                                        return null;
                                    })}
                                </div>
                            </div>
                        }
                    </div>
                })}
            </>
        }

        <div className="test__buttons finished">
            {test.status === statusType.finished &&
                <NavLink className="button button-orange" to={nextLessonLink}>Следующий урок</NavLink>
            }

            <button
                className="button button-gray"
                onClick={restartTest}
            >Пройти заново
            </button>
        </div>
    </>;
};

export default TestFinishedContent;
