import React from 'react';
import TinySlider from "tiny-slider-react";
import {useSelector} from "react-redux";

const SliderTop = () => {

    const slides = useSelector(state => state.mainPageReducer.topSectionSlides);

    const settings = {
        items: 1,
        mouseDrag: true,
        loop: false,
        rewind: true,
        controls: false,
        navPosition: 'bottom',
        autoplay: false,
        mode: 'gallery',
        animateIn: 'tns-fadeIn',
        animateOut: 'tns-fadeOut',
        animateDelay: 600,
        responsive: {
            480: {
            },
            992: {
            }
        }
    };

    return <div className="sliderTopOuter">
        <TinySlider className="sliderTop" settings={settings}>
            {slides.map((slide, index) => (
                <div key={ index } className="element">
                    <div className="element__inner">
                        <div className="col col-img">
                            <img src={ slide.img } alt=""/>
                        </div>

                        <div className="col col-text">
                            <p className="sliderTop__title">{ slide.title }</p>

                            <div className="sliderTop__bottom">
                                <p className="sliderTop__description">{ slide.description }</p>

                                <button className="button">{ slide.buttonText }</button>
                            </div>
                        </div>
                    </div>
                </div>
            ))}
        </TinySlider>
    </div>;
};

export default SliderTop;
