import React, {useRef} from 'react';
import TinySlider from "tiny-slider-react";
import SliderVideoElement from "./slide/slide";

const SliderVideo = ({ toggleButtonText, list, onToggleButtonClick }) => {
    const ts = useRef(null);

    const settings = {
        items: 2,
        slideBy: 1,
        gutter: 30,
        mouseDrag: true,
        loop: false,
        rewind: true,
        controls: false,
        nav: false,
        autoplay: false,
        mode: 'carousel',
        animateDelay: 600,
        responsive: {
            0: {
                items: 1,
            },
            568: {
                items: 2,
            }
        }
    };

    const onGoTo = dir => {
        if (ts) {
            ts.current?.slider.goTo(dir);
        }
    }

    return <div>
        <TinySlider
            className="sliderVideo"
            ref={ ts }
            settings={ settings }
        >
            { list && list.map((video, index) => {
                return <SliderVideoElement
                    data={ video }
                    key={ index }
                />;
            })}
        </TinySlider>

        <div className="sliderVideoBottom">
            { toggleButtonText &&
                <button
                    className="more"
                    onClick={() => onToggleButtonClick(false)}
                >
                    <span className="icon icon-arr_rounded_right"/>
                    <span className="text">{ toggleButtonText }</span>
                </button>
            }

            <div className="sliderNav">
                <button className="arr-left" onClick={() => onGoTo('prev')}><span/></button>
                <button className="arr-right" onClick={() => onGoTo('next')}><span/></button>
            </div>
        </div>
    </div>;
};

export default SliderVideo;
