import React from 'react';
import * as actionTypes from "../../../../redux/actionTypes";
import {useDispatch, useSelector} from "react-redux";
import PlayButton from "../../../common/playButton/playButton";

import './slide.sass';
import ModalPortal from "../../../portals/modalPortal/modalPortal";
import Modal from "../../../common/modal/modal";
import Player from "../../../common/player/player";

const SliderVideoElement = ({ data }) => {
    const dispatch = useDispatch();
    const openedModalName = useSelector(state => state.mainReducer.modalName);

    const nameForModal = 'videoSlide_' + data.id;

    const showModal = name => {
        dispatch({ type: actionTypes.SET_MODAL_NAME, params: { name } });
    };

    return <div className="slide" key={ data.id }>
        <div className="element" onClick={() => showModal(nameForModal)}>
            <div className="col col-img">
                <img src={ data.img } alt={ data.name } />

                <PlayButton onClick={() => showModal(nameForModal)} />
            </div>

            <div className="col col-text">
                <p className="title">{ data.name }</p>

                <p className="bottomText">{ data.time }</p>
            </div>
        </div>

        { !!openedModalName && openedModalName === nameForModal && <ModalPortal>
            <Modal name={ nameForModal }>
                <Player
                    url={ data.videoUrl }
                    isAutoplay={ true }
                />
            </Modal>
        </ModalPortal> }
    </div>;
};

export default SliderVideoElement;
