import React, {useEffect, useState} from 'react';
import ReactPlayer from "react-player/lazy";

import PlayerProgressLine from "./progressLine/progressLine";
import PlayerOverlay from "./overlay/overlay";
import PlayButtonBig from "./playButtonBig/playButtonBig";
import PlayerControls from "./controls/controls";

import './player.sass';
import {useDispatch} from "react-redux";
import * as actionTypes from "../../../redux/actionTypes";
import {secondsToTime} from "../../../helpers";
import {courseActions} from "../../../redux/actions/courseActions";
import {courseThunks} from "../../../redux/thunks/courseThunks";
import {statusType} from "../../../types";

const Player = ({ url, width, height, title, description, time, playerType, isAutoplay, onPause, onStop }) => {
    const dispatch = useDispatch();

    const player = React.createRef();
    const playerWrapper = React.createRef();

    let hoveredTimeout;

    const config = {
        youtube: {
            playerVars: {
                autoplay: isAutoplay ? 1 : 0,
                playsinline: 1,
                showinfo: 0,
                rel: 0,
                iv_load_policy: 3
            },
            preload: true
        }
    };

    const [played, setPlayed] = useState(0);
    const [volume, setVolume] = useState(1);
    const [loaded, setLoaded] = useState(0);

    const [isPlaying, changePlayingState] = useState(false);
    const [isMuted, changeMutedState] = useState(false);
    const [isExternalVideo, setAsExternal] = useState(false);
    const [isTouched, setTouched] = useState(false);
    const [isHovered, changeHoveredState] = useState(false);

    useEffect((e) => {
        if (url && url.includes('youtube.com')) {
            setAsExternal(true);
        }

        if (isAutoplay && !isExternalVideo) {
            changePlayingStateHandler(true);
        }

        return (e) => {
            changePlayingStateHandler(false);
        }
    }, []); // eslint-disable-line react-hooks/exhaustive-deps

    const changePlayingStateHandler = (data = null) => {
        if (isPlaying && !isTouched) {
            setTouched(true);
        }

        changePlayingState(data !== null ? data : !isPlaying);
    };

    const changeHoveredStateHandler = () => {
        if (!isHovered) {
            changeHoveredState(true);

            clearTimeout(hoveredTimeout);

            hoveredTimeout = setTimeout(() => {
                changeHoveredState(false);
            }, 3000);
        }
    };

    const seekMouseUp = value => player.current.seekTo(value);
    const seekChange = value => setPlayed(value);

    const getProgressData = (data) => {
        const isFinished = data.played > 0.95;

        setPlayed(data.played);
        setLoaded(data.loaded);

        if (playerType === 'videoLesson' && data.playedSeconds > 0) {
            dispatch(courseActions.saveVideoProgress({
                time: secondsToTime(data.playedSeconds),
                isFinished
            }));

            if (isFinished) {
                dispatch(courseThunks.setLessonVideoIsFinished());
            }
        }

        if (isFinished) {
            handleStop();
        }
    };

    const handlePause = () => {
        onPause();
    };

    const handleStop = () => {
        onStop();
    };

    // -----------------------------

    return <div
        ref={ playerWrapper }
        className={`reactPlayerWrapper player${!isPlaying ? ' paused' : ''}${isTouched ? ' touched' : ''}${isHovered ? ' hovered' : ''}`}
        style={{ paddingTop: !!width && !!height ? 100 / (width / height) + '%' : null }}
        onMouseMove={ changeHoveredStateHandler }
    >
        { !isExternalVideo ?
            <PlayerOverlay onClick={ changePlayingStateHandler } />
            : !isPlaying && isTouched && <PlayButtonBig onClick={ changePlayingStateHandler } /> }

        <div className="player__controls">
            { (!!title || !!description) && <div className="player__text">
                { !!title && <p className="player__title">{ title }</p> }
                { !!description && <p className="player__description">{ description }</p> }
            </div> }

            <button
                className={`playSmall icon icon-${isPlaying ? 'pause' : 'play'}`}
                onClick={ () => changePlayingState(!isPlaying) }
            />

            <PlayerProgressLine
                progress={ played }
                buffer={ loaded }
                onSeekMouseUp={ seekMouseUp }
                onSeekChange={ seekChange }
            />

            <PlayerControls
                playerWrapper={ playerWrapper }
                volume={ volume }
                isMuted={ isMuted }
                updateVolume={ (value) => setVolume(value) }
                updateMuted={ (value) => changeMutedState(value) }
            />
        </div>

        <ReactPlayer
            ref={ player }
            className="reactPlayer"
            url={ url }
            config={ config }
            playing={ isPlaying }
            playIcon={ <PlayerOverlay
                onClick={ changePlayingStateHandler }
            /> }
            volume={ volume }
            muted={ isMuted }
            light={ isExternalVideo }
            width="100%"
            height="100%"

            onPause={() => {
                if (isExternalVideo) {
                    changePlayingStateHandler(false);
                }
                handlePause();
            }}
            onPlay={() => {
                if (isExternalVideo) {
                    changePlayingStateHandler(true);
                }
            }}

            onStart={() => {
                if (time > 0) {
                    player.current.seekTo(time, 'seconds');
                }
            }}

            onProgress={e => getProgressData(e)}
            onEnded={() => handleStop()}
        />
    </div>
};

export default Player;
