import React from 'react';
import PlayButtonBig from "../playButtonBig/playButtonBig";

import './overlay.sass';

const PlayerOverlay = ({ onClick }) => {

    const handlerClick = () => onClick();

    return <div className="playerOverlay">
        <div
            className="before"
            onClick={ handlerClick }
        />

        <PlayButtonBig onClick={ handlerClick } />
    </div>
};

export default PlayerOverlay;
