/**
 * Перевод строковых значений, полученных от сервера, вида "0" или "1" в логические false или true соответственно.
 * @param text - значение, которое нужно перевести.
 * @returns {boolean}
 */
import {statusType} from "../types";

export const getBoolFromString = value => {
    if (typeof value === 'string') {
        return value === '1' || value.toLowerCase() === 'true';
    }

    if (typeof value === 'number') {
        return value === 1;
    }

    return false;
}

/**
 * Возвращает дату в формате 2000-12-21
 *
 * @param date - если не задана, берется текущая дата
 * @returns {Date}
 */
export const getDateString = (date = new Date()) => {
    const offset = date.getTimezoneOffset()
    const dateWithOffset = new Date(date.getTime() - (offset*60*1000));

    return dateWithOffset.toISOString().split('T')[0];
}

/**
 * конвертация секунд (number) в строку типа hh:mm:ss
 *
 * @param time
 * @returns {string}
 */
export const secondsToTime = time => {
    const floorTime = Math.floor(time);

    return new Date(floorTime * 1000).toISOString().substr(11, 8);
}

/**
 * проверка, нужно ли менять статус теста в зависимости от текущего и
 * предлагаемого статуса.
 *
 * предполагаемые варианты:
 * - может стать только "доступным" только если он сейчас "заблокирован"
 * - "начатым" - только если "доступен", "завершен" или "завершен с ошибкой"
 * - "завершенным" либо "завершенным с ошибкой" - только если "начат"
 *
 * @param current - текущий статус теста
 * @param target - статус, который юзер пытается применить
 * @returns {boolean}
 */
export const shouldTestTypeChange = (current, target) => {
    if (target === statusType.enabled && current === statusType.disabled) {
        return true;
    }

    if (target === statusType.started && (current === statusType.enabled || current === statusType.finished || current === statusType.finishedWithErrors)) {
        return true;
    }

    if ((target === statusType.finished || target === statusType.finishedWithErrors) && current === statusType.started) {
        return true;
    }

    return false;
}
