import * as actionTypes from '../actionTypes';

const initialState = {
    /**
     * Данные пользователя
     */
    user: {
        id: null,
        isAuthorised: false,
        isAdmin: false,
        isPayGS: false,
        isPayRadar: false,
        isPayRadarUSA: false
    },

    /**
     * Список рекомендованных статей для вывода в соответствующий слайдер
     */
    articles: [],

    /**
     * Указание на то, заблокирован ли скролл страницы или нет
     */
    isWindowBlocked: false,

    /**
     * Имя открытого в данный момент модального окна
     */
    modalName: '',
};

const mainReducer = (state = initialState, action) => {
    switch (action.type) {
        case actionTypes.SET_USER_DATA: {
            return {
                ...state,
                user: action.params
            }
        }

        case actionTypes.SET_ARTICLES: {
            return {
                ...state,
                articles: action.params.articles
            };
        }

        case actionTypes.SET_MODAL_NAME: {
            return {
                ...state,
                isWindowBlocked: !!action.params.name,
                modalName: action.params.name
            };
        }

        case actionTypes.CLEAR_MODAL_NAME: {
            return {
                ...state,
                isWindowBlocked: false,
                modalName: ''
            };
        }

        default: return state;
    }
};

export default mainReducer;
