import React, {useEffect, useState} from "react";

const RadioButtonsList = ({ list, name, onChange }) => {
    const [value, setValue] = useState(-1);

    useEffect(() => {
        list.forEach(radio => {
           if (radio.isChecked) {
               setValue(radio.id);
           }

           return;
        });
    }, []); // eslint-disable-line react-hooks/exhaustive-deps

    useEffect(() => {
        list.forEach(radio => {
            if (radio.isChecked) {
                setValue(radio.id);
            }

            return;
        });
    }, [value, list]); // eslint-disable-line react-hooks/exhaustive-deps

    const handleOnChange = (event, value) => {
        onChange(event, value);
        setValue(value)
    };

    return <div className="radioButtonsList">
        { list.map(radio => {
            return <label
                key={ radio.id }
                className="radio"
            >
                <input
                    type="radio"
                    name={ radio.name }
                    value={ value }
                    checked={ value === radio.id }
                    disabled={ radio.isDisabled }
                    onChange={ event => handleOnChange(event.target.checked, radio.value) }
                />

                <span className="radio__inner">
                    <span className="dot" />

                    <span className="text">{ radio.text }</span>
                </span>
            </label>;
        })}

    </div>;
};

export default RadioButtonsList;
