import React, {useState} from 'react';
import VideosTable from "../../../components/blocks/videosTable/videosTable";
import SliderVideo from "../../../components/blocks/sliderVideo/sliderVideo";

const CourseAnswers = ({ list }) => {
    const [isSliderView, toggleView] = useState(true);

    const handleToggleView = (e) => {
        toggleView(e);
    }

    return <div className="sliderVideoOuter">
        <div className="container">
            <h2 className="titleBordered">
                Ответы на частые вопросы
                { list && list.length > 0 &&
                <span className="number">{ list.length }</span>
                }
            </h2>

            { isSliderView ?
                <SliderVideo
                    list={ list }
                    toggleButtonText={ 'Посмотреть все' }
                    onToggleButtonClick={ handleToggleView }
                />
                :
                <VideosTable
                    list={ list }
                    toggleButtonText={ 'Свернуть' }
                    onToggleButtonClick={ handleToggleView }
                />
            }
        </div>
    </div>;
};

export default CourseAnswers;
