import React, {useState} from 'react';
import {buttonMoreType, courseMaterialsType} from "../../../types";

import ButtonMore from "../../common/buttonMore/buttonMore";

const FilesList = ({ files }) => {

    /**
     * Максимальное количество отображаемых файлов в скрытом виде
     *
     * @type {number}
     */
    const MAX_VISIBLE_FILES = 4;

    const [isOpened, toggleIsOpened] = useState(false);

    const getBtnIcon = type => {
        switch (type) {
            case courseMaterialsType.link:
                return 'book';
            case courseMaterialsType.file:
                return 'download';
            default:
                return 'book';
        }
    }

    const getBtnText = type => {
        switch (type) {
            case courseMaterialsType.link:
                return 'Читать';
            case courseMaterialsType.file:
                return 'Скачать';
            default:
                return 'Читать';
        }
    }

    const onClickToggleButton = () => {
        toggleIsOpened(!isOpened);
    }

    return <div className="courseFiles">
        <div className="container">
            <h2 className="titleBordered">Дополнительные материалы <span className="number">{ files.length }</span></h2>

            <ul className="courseFiles__list">
                { files.map((file, index) => {
                    if (index < MAX_VISIBLE_FILES || isOpened) {
                        return <li key={ index }>
                            <a
                                href={ file.link }
                                target="_blank"
                                rel="noreferrer"
                                className="courseFiles__element"
                                download={file.type === courseMaterialsType.file}
                            >
                                <span className="title">{ file.name }</span>

                                <span className="btn">
                                    <span className={`icon icon-${ getBtnIcon(file.type) }`}/>
                                    <span className="text">{ getBtnText(file.type) }</span>
                                </span>
                            </a>
                        </li>;
                    }

                    return null;
                }) }
            </ul>

            { files.length > MAX_VISIBLE_FILES &&
                <ButtonMore
                    type={ buttonMoreType.button }
                    handler={ onClickToggleButton }
                    subclass={ 'courseFiles__toggle' }
                >{ isOpened ? 'Свернуть' : 'Посмотреть все' }</ButtonMore>
            }
        </div>
    </div>;
};

export default FilesList;
