import React, {useEffect} from 'react';
import {useDispatch, useSelector} from "react-redux";
import LessonVideo from "./video/video";
import FilesList from "../../components/blocks/filesList/filesList";
import LoaderBlock from "../../components/common/loaderBlock/loaderBlock";
import Test from "./test/test";
import {courseThunks} from "../../redux/thunks/courseThunks";

import './lesson.sass';
import {useHistory} from "react-router-dom";
import {statusType} from "../../types";

const LessonPage = (props) => {
    const dispatch = useDispatch();

    const userId = useSelector(state => state.mainReducer.user.id);
    const course = useSelector(state => state.courseReducer.course);
    const lesson = useSelector(state => state.courseReducer.lesson);
    const test = lesson.test;

    const isCourseLoaded = useSelector(state => state.courseReducer.isCourseLoaded);
    const isLessonLoaded = useSelector(state => state.courseReducer.isLessonLoaded);
    const isTestLoaded = useSelector(state => state.courseReducer.isTestLoaded);

    let history = useHistory().location.pathname;

    useEffect(() => {
        return () => {
            setCurrentTime();
        };
    }, []); // eslint-disable-line react-hooks/exhaustive-deps

    /**
     * Загрузка данных урока при изменении роута
     * (переход на другой урок по клику на ссылку справа от видео
     * либо под завершенным тестом)
     */
    useEffect(() => {
        const courseId = props.match.params.courseId;
        const lessonId = props.match.params.lessonId;

        if (isLessonLoaded) {
            dispatch(courseThunks.getLessonData(courseId, lessonId));
        }
    }, [history]); // eslint-disable-line react-hooks/exhaustive-deps

    /**
     * Загрузка актуальных данных теста
     */
    useEffect(() => {
        if (test && !isTestLoaded) {
            dispatch(courseThunks.parseTestData());
        }
    }, [test, isLessonLoaded]); // eslint-disable-line react-hooks/exhaustive-deps

    /**
     * Загрузка данных урока при получении всех необходимых
     * для этого данных
     */
    useEffect(() => {
        const courseId = props.match.params.courseId;
        const lessonId = props.match.params.lessonId;

        if (userId && courseId && lessonId && lessonId !== lesson?.id && !isLessonLoaded) {
            dispatch(courseThunks.getLessonData(courseId, lessonId));
        }
    }, [props, userId]); // eslint-disable-line react-hooks/exhaustive-deps

    const setCurrentTime = () => {
        if (lesson.video) {
            dispatch(courseThunks.updateVideoProgress({
                time: lesson.video.time,
                isFinished: lesson.video.isFinished
            }));
        }
    };

    const setVideoFinished = () => {
        dispatch(courseThunks.updateVideoProgress({
            time: 0,
            isFinished: true
        }));
    };

    const getLessonContent = () => {
        return <>
            <div className="lessonVideoOuter">
                { lesson.video &&
                    <LessonVideo
                        lessons={course.lessons}
                        lesson={lesson}
                        onVideoPause={setCurrentTime}
                        onVideoStop={setVideoFinished}
                    />
                }

                { lesson.test && course.lessons.find(less=>less.id===lesson.id).status !== statusType.disabled &&
                    <div className="container">
                        {/*<Test test={ lesson.test } />*/}

                        { isTestLoaded ?
                            <Test />
                            :
                            <div className="loaderTest">
                                <LoaderBlock className="loaderTest__icon" />
                                <div className="loaderTest__text">
                                    <LoaderBlock />
                                    <LoaderBlock />
                                </div>
                                <LoaderBlock className="loaderTest__btn" />
                            </div>
                        }
                    </div>
                }
            </div>

            { course.materials && course.lessons.find(less=>less.id===lesson.id).status !== statusType.disabled &&
                <FilesList files={course.materials}/>
            }
        </>;
    };

    return <div className="pageLesson">
        { isCourseLoaded && isLessonLoaded ?
            getLessonContent()
            :
            <div className="container">
                <LoaderBlock className="loaderVideo" />
            </div>
        }
    </div>;
};

export default LessonPage;
