import React from 'react';
import VideoElement from "../../common/videoElement/videoElement";

import './videosList.sass';

const CoursesList = ({ category, list }) => {
    return <div className="videosListOuter" id={ category.code }>
        {category &&
            <h2 className="titleBordered">{ category.name } <span className="number">{ list.length }</span></h2>
        }

        <div className="videosList">
            {list.map((element, id) => {
                return <VideoElement data={ element } key={ id } category={ category.code } />
            })}
        </div>
    </div>;
};

export default CoursesList;
