import {courseActions} from "../actions/courseActions";
import {statusType, testQuestionType} from "../../types";
import {coursePageAPI} from "../../api";
import {getDateString, shouldTestTypeChange} from "../../helpers";

export const courseThunks = {
    /**
     * Получение данных учебного курса
     * Данные включают в себя список уроков, файлов и список видео
     * с ответами на воросы
     * @param id - идентификатор курса
     * @returns {(function(*): void)|*}
     */
    getCourseData: id => (dispatch, getState) => {
        dispatch(courseActions.setCourseNotLoaded());

        coursePageAPI.getCourseData(id)
            .then(result => {
                const userId = getState().mainReducer.user.id;

                let course = {
                    id: parseInt(result['ID']),
                    name: result['UF_NAME'],
                    description: result['UF_DESCRIPTION'],
                    isPurchased: getState().mainPageReducer.payedCoursesList.includes(parseInt(result['ID'])),
                    hasTest: result['COUNT_TESTS'] > 0,
                    price: parseInt(result['UF_PRICE']),
                    countLessons: result['COUNT_LESSONS'],
                    countTests: result['COUNT_TESTS'],

                    lessons: result['LESSONS'].map((lesson, index) => {
                        return {
                            id: lesson['ID'],
                            name: lesson['UF_NAME'],
                            image: lesson['UF_PICTURE']['SRC'],
                            status: index === 0 ? statusType.enabled : statusType.disabled,
                            hasTest: lesson['COUNT_TESTS'] > 0,
                            testId: lesson['TESTS_ID'] ? lesson['TESTS_ID'] : -1,
                            testStatus: lesson['TESTS_ID'] ? statusType.disabled : null
                        }
                    })
                };

                coursePageAPI.getCourseStatistics(userId, course.id)
                    .then(statistics => {
                        course.lessons = course.lessons.map(lesson => {
                            const changedLesson = statistics['UF_LESSONS'].find(statLesson => statLesson.id === lesson.id);

                            if (changedLesson) {
                                lesson.status = changedLesson.status;
                            }

                            if (lesson.testId >= 0) {
                                const changedTest = statistics['UF_TESTS'].find(staTest => staTest.id === lesson.testId);

                                if (changedTest) {
                                    lesson.testStatus = changedTest.status;
                                }
                            }

                            return lesson;
                        });

                        dispatch(courseActions.setCourseData({course}));

                        dispatch(courseActions.setCourseLoaded());
                    })
                    .catch(error => {
                        console.log(`Ошибка получения статистики курса, id: ${result.ID}`, error);

                        dispatch(courseActions.setCourseData({course}));

                        dispatch(courseActions.setCourseLoaded());
                    });
            })
            .catch(error => {
                console.log(`Ошибка получения списка уроков для курса, id: ${id}\n`, error);
                dispatch(courseActions.setCourseLoaded());
            });
    },

    /**
     * Получение данных урока
     * @param courseId - идентификатор курса
     * @param lessonId - идентификатор урока
     * @returns {(function(*): void)|*}
     */
    getLessonData: (courseId, lessonId) => (dispatch, getState) => {
        const hasCourse = getState().courseReducer.course.id;
        const userId = getState().mainReducer.user.id;

        if (!hasCourse) {
            dispatch(courseThunks.getCourseData(courseId));
        }

        dispatch(courseActions.setLessonNotLoaded());

        coursePageAPI.getLessonData(userId, lessonId)
            .then(response => {

                console.log('!!!', response)
                const getPoints = (questionType, answers) => {
                    let pointsToPass = 0;

                    if (questionType === testQuestionType.scale) {
                        pointsToPass += answers[0].points;
                    } else {
                        answers.forEach(answer => {
                            pointsToPass += answer.points
                        });
                    }

                    return pointsToPass;
                };

                const getQuestions = (questions) => {
                    const questionsArray = [];

                    Object.keys(questions).forEach(index => {
                        const questionRaw = questions[index];

                        if (typeof questionRaw === 'object') {
                            let pointsToPass = 0;

                            const question = {
                                id: questionRaw['ID'],
                                question: questionRaw['UF_NAME'],
                                description: questionRaw['UF_DESCRIPTION'],
                                imageUrl: questionRaw['UF_PICTURE'],
                                type: questionRaw['UF_TYPE']['UF_TYPE_CODE'], // scale, multiply, once
                                answers: questionRaw['ANSWERS'].map(answer => {
                                    pointsToPass += answer.points;

                                    return {
                                        id: answer['ID'],
                                        text: answer['UF_NAME'],
                                        isRight: answer['UF_IS_TRUE_ANSWER'],
                                        points: answer['UF_AWARD_SCORE'],
                                        isChecked: false,
                                        isUserAnswer: answer['UF_USER_ANSWER'] === 1
                                    }
                                })
                            }

                            question.points = getPoints(question.type, question.answers);

                            questionsArray.push(question);
                        }
                    })

                    return questionsArray;
                }

                const lesson = {
                    id: parseInt(response['ID']),
                    courseId: response['UF_COURSE_ID'],
                    name: response['UF_NAME'],
                    status: statusType.enabled,
                    video: {
                        id: parseInt(response['VIDEO']['ID']),
                        // url: response['VIDEO']['UF_VIDEO_LINK'],
                        url: 'https://www.youtube.com/watch?v=668nUCeBHyY',
                        // TODO заменить на нормальное видео
                        // когда появится первый реальный курс
                        duration: response['VIDEO']['UF_DURATION'],
                        time: response['VIDEO']['STATS'] ? response['VIDEO']['STATS']['UF_TIME'] : '00:00:00',
                        isFinished: response['VIDEO']['STATS'] ? response['VIDEO']['STATS']['UF_VIEWED'] : false
                    },
                    test: response['TEST'] && response['TEST']['ID'] ? {
                        id: parseInt(response['TEST']['ID']),
                        name: response['TEST']['UF_NAME'],
                        description: response['TEST']['UF_DESCRIPTION'],
                        status: response['TEST']['STATUS'],
                        previewImg: response['TEST']['UF_PICTURE'],
                        collectedPoints: 0,
                        pointsToPass: response['TEST']['UF_SUCCESSFULL_AWARD'],
                        currentQuestionIndex: 0,
                        questions: getQuestions(response['TEST']['QUESTIONS']),
                        userAnswers: response['TEST']['USER_ANSWERS'].map(answer => {
                            return {
                                testId: answer['UF_TEST_ID'],
                                questionId: answer['UF_QUESTION_ID'],
                                answerId: answer['UF_ANSWER_ID']
                            }
                        })
                    } : null
                };

                dispatch(courseActions.setLessonData({lesson}));

                dispatch(courseActions.setLessonLoaded());
            })
            .catch(error => {
                console.log(`Ошибка получения данных урока, id: ${lessonId}\n`, error);

                dispatch(courseActions.setLessonLoaded());


            });
    },

    /**
     * Сохранение текущего времени просмотра
     * для видео на сервере и в сторе
     *
     * @param params
     * @returns {(function(*, *): void)|*}
     */
    updateVideoProgress: params => (dispatch, getState) => {
        const store = getState();
        const lesson = store.courseReducer.lesson;
        const userId = store.mainReducer.user.id;
        const lessonId = lesson.id;
        const videoId = lesson.video.id;

        coursePageAPI.setVideoProgress(userId, lessonId, videoId, params.time, params.isFinished)
            .catch(error => {
                console.log('Неудачная попытка сохранения прогресса видео на сервере. \nvideoId: ', videoId, error);
            });
    },

    /**
     * Метод срабатывает при окончании видео урока.
     * Если в уроке есть тест - он становится доступен.
     * Если нет - урок завершается, становится доступен следующий.
     * @returns {(function(*, *): void)|*}
     */
    setLessonVideoIsFinished: () => (dispatch, getState) => {
        const store = getState();
        const lesson = store.courseReducer.lesson;
        const test = lesson.test;

        if (test) {
            dispatch(courseThunks.setTestStatus(statusType.enabled));
        } else {
            dispatch(courseThunks.setLessonIsFinished());
        }
    },

    /**
     * Обновление статуса теста
     * Передаются id юзера, курса и самого теста, новый статус теста
     * и текущая дата
     *
     * @param testStatus
     * @returns {(function(*, *): void)|*}
     */
    setTestStatus: testStatus => (dispatch, getState) => {
        const store = getState();
        const userId = store.mainReducer.user.id;
        const course = store.courseReducer.course;
        const lesson = store.courseReducer.lesson;
        const courseId = course.id;
        const lessonId = lesson.id;
        const test = lesson.test;
        const updateDate = getDateString();

        let nextLessonId = -1;
        course.lessons.forEach((lesson, index) => {
           if (lesson.id === lessonId) {
               nextLessonId = course.lessons[index + 1].id;

               return;
           }
        });

        if (shouldTestTypeChange(test.status, testStatus)) {
            dispatch(courseActions.setTestStatus({status: testStatus}));
        }

        if (courseId > -1 && test && test.id > -1) {
            coursePageAPI.updateTestStatus({userId, courseId, updateDate, testId: test.id, testStatus});
        }

        if (testStatus === statusType.finished) {
            dispatch(courseThunks.setLessonIsFinished());
        }
    },

    /**
     * Обновление статуса урока
     * @returns {(function(*, *): void)|*}
     */
    setLessonIsFinished: () => (dispatch, getState) => {
        const store = getState();
        const userId = store.mainReducer.user.id;
        const course = store.courseReducer.course;
        const lesson = store.courseReducer.lesson;
        const courseId = course.id;
        const lessonId = lesson.id;

        let nextLessonId = -1;
        course.lessons.forEach((lesson, index) => {
            if (lesson.id === lessonId) {
                nextLessonId = course.lessons[index + 1].id;

                return;
            }
        });

        const lessons = course.lessons.map(currentLesson => {
            if (currentLesson.id === lessonId) {
                currentLesson.status = statusType.finished;
            } else if (currentLesson.id === nextLessonId) {
                currentLesson.status = statusType.enabled;
            }

            return currentLesson;
        });

        dispatch(courseActions.updateLessonStatus({lessons}));

        coursePageAPI.updateLessonStatus({userId, courseId, updateDate: getDateString(), lessonId, lessonStatus: statusType.finished})
            .then(response => {
                console.log('+',response)
            });

        if (nextLessonId >= 0) {
            coursePageAPI.updateLessonStatus({userId, courseId, updateDate: getDateString(), lessonId: nextLessonId, lessonStatus: statusType.enabled})
                .then(response => {
                    console.log('+',response)
                });
        }
    },

    /**
     * Получение списка доступных вопросов
     * @returns {(function(*): void)|*}
     */
    getTestQuestionTypes: () => dispatch => {
        coursePageAPI.getTestQuestionTypes()
            .then(result => {
                dispatch(courseActions.setTestQuestionTypes({
                    list: result.map(type => {
                        return {
                            id: parseInt(type['ID']),
                            name: type['UF_TYPE_CODE'],
                            title: type['UF_NAME'],
                            description: type['UF_DESCRIPTION']
                        };
                    })
                }));
            })
            .catch(error => {
                console.log('Ошибка получения списка типов вопросов для тестов. \n', error);
            });
    },

    /**
     * Обработка данных теста после их получения от сервера.
     * Метод обходит все вопросы и ответы теста, подсчитывает
     * набранные очки и определяет индекс вопроса,
     * на котором остановился юзер
     *
     * @returns {(function(*))|*}
     */
    parseTestData: () => (dispatch, getState) => {
        const store = getState();
        const test = store.courseReducer.lesson.test;
        const userAnswers = test.userAnswers;
        let questions = test.questions;

        let currentQuestionIndex = 0;
        let collectedPoints = 0;

        questions = questions.map(question => {
            if (userAnswers.find(a => a.questionId === question.id)) {
                question.answers = question.answers.map(answer => {
                    if (userAnswers.find(a => a.answerId === answer.id)) {
                        answer.isChecked = true;

                        collectedPoints += answer.points;
                    }

                    return answer;
                });

                if (currentQuestionIndex < questions.length - 1) {
                    currentQuestionIndex++;
                }
            }

            return question;
        });

        dispatch(courseActions.setParsedTestData({currentQuestionIndex, collectedPoints, questions}));
    },

    /**
     * Сохранение варианта ответа юзера на вопрос теста
     * @returns {(function(*, *): void)|*}
     */
    updateTestAnswers: ({answers, testId, questionId, answerId, isChecked}) => (dispatch, getState) => {
        const store = getState();
        const uid = store.mainReducer.user.id;
        const questionTypes = store.courseReducer.testQuestionTypes;

        const test = store.courseReducer.lesson.test;
        const question = test.questions.find(q => q.id === questionId);
        const questionType = questionTypes.find(type => type.name === question.type).id;

        dispatch(courseActions.updateTestAnswersStatus({answers}));

        if (typeof questionType === 'number') {
            if (question.type === testQuestionType.multiply && !isChecked) {
                coursePageAPI.deleteTestAnswer({uid, testId, questionId, answerId});
            } else {
                coursePageAPI.updateTestAnswer({uid, testId, questionId, questionType, answerId});
            }
        } else {
            console.log("Не удалось сохранить вариант ответа на вопрос теста на сервере. Причина: не удалось" +
                " получить id типа вопроса.");
        }
    },

    /**
     * Сохранение варианта ответа юзера
     * @returns {(function(*, *): void)|*}
     */
    deleteTestAnswer: ({testId, questionId, answerId}) => (_, getState) =>  {
        const store = getState();
        const uid = store.mainReducer.user.id;

        coursePageAPI.deleteTestAnswer({uid, testId, questionId, answerId});
    },

    /**
     * Перезапуск текущего теста с очисткой ответов на вопросы
     * @returns {(function(*, *): void)|*}
     */
    restartTest: () => (dispatch, getState) => {
        const store = getState();
        const uid = store.mainReducer.user.id;
        const test = store.courseReducer.lesson.test;

        coursePageAPI.deleteAllTestAnswers({uid, testId: test.id})
            .then(_ => {
                dispatch(courseThunks.setTestStatus(statusType.started));
                dispatch(courseThunks.deleteAllTestAnswers());
            })
            .catch(error => {
                console.log('Неудачная попытка сброса теста. \ntest id: ', test.id, error);
            });
    },

    /**
     * Сброс всех ответов пользователя на тест (локальный)
     *
     * @returns {(function(*, *): void)|*}
     */
    deleteAllTestAnswers: () => (dispatch, getState) => {
        const store = getState();
        const test = store.courseReducer.lesson.test;

        const questions = test.questions.map(question => {
            question.answers = question.answers.map(answer => {
                answer.isChecked = false;

                return answer;
            });

            return question;
        });

        dispatch(courseActions.deleteAllTestAnswers({questions}));
    }
}
