import * as actionTypes from "../actionTypes";

export const mainActions = {
    /**
     * Сохранение полученных от сервера статей для слайдера на странице курса
     * @param params
     * @returns {{type: string, params}}
     */
    setArticles: params => ({
        type: actionTypes.SET_ARTICLES,
        params
    }),

    /**
     * Сохранение данных текущего авторизованного пользователя
     * @param params
     * @returns {{type: string, params}}
     */
    setUserData: params => ({
        type: actionTypes.SET_USER_DATA,
        params
    })
}
