import React, {useRef} from 'react';
import TinySlider from "tiny-slider-react";

const SectionArticles = ({ articles }) => {
    const ts = useRef(null);

    const settings = {
        items: 4,
        slideBy: 1,
        gutter: 30,
        mouseDrag: true,
        loop: false,
        rewind: true,
        controls: false,
        nav: false,
        autoplay: false,
        mode: 'carousel',
        animateDelay: 600,
        responsive: {
            0: {
                items: 1,
            },
            568: {
                items: 2,
            },
            992: {
                items: 3,
            },
            1366: {
                items: 4,
            }
        }
    };

    const onGoTo = dir => {
        if (ts) {
            ts.current?.slider.goTo(dir);
        }
    }

    return <div className="sliderArticlesOuter">
        <div className="container">
            <h2 className="titleBordered">
                Рекомендуем
                { articles.length > 0 &&
                    <span className="number">{ articles.length }</span>
                }
            </h2>

            <TinySlider
                className="sliderArticles"
                ref={ ts }
                settings={ settings }
            >
                { articles && articles.map((article, index) => {
                    return <div className="slide" key={ index }>
                        <a
                            href={ article.link }
                            target="_blank"
                            rel="noreferrer"
                            className="element"
                        >
                            <span className="img">
                                <img src={ article.img } alt={ article.name } />
                            </span>

                            <span className="text">
                                <span className="category">{ article.category }</span>

                                <span className="title">{ article.name }</span>
                            </span>
                        </a>
                    </div>;
                })}
            </TinySlider>

            <div className="sliderArticlesBottom">
                <a
                    href="https://fin-plan.org/blog/"
                    target="_blank"
                    rel="noreferrer"
                    className="more"
                >
                    <span className="icon icon-arr_rounded_right" />
                    <span className="text">Посмотреть все</span>
                </a>

                <div className="sliderNav">
                    <button className="arr-left" onClick={() => onGoTo('prev')}><span /></button>
                    <button className="arr-right" onClick={() => onGoTo('next')}><span /></button>
                </div>
            </div>
        </div>
    </div>;
};

export default SectionArticles;
