import MainPage from "../pages/mainpage/mainpage";
import CoursePage from "../pages/course/course";
import ErrorPage from "../pages/error/error";
import LessonPage from "../pages/lesson/lesson";

import SliderTop from "../pages/mainpage/sliderTop/sliderTop";
import CategoriesNav from "../pages/mainpage/categoriesNav/categoriesNav";

import SectionTopCourse from "../pages/course/sectionTop/sectionTop";
import CoursePanel from "../pages/course/coursePanel/coursePanel";

import SectionTopLesson from "../pages/lesson/sectionTop/sectionTop";

export const path = [
    {
        path: '/blog',
        isExact: true,
        components: {
            main: MainPage,
            topSection: SliderTop,
            panel: CategoriesNav
        }
    },
    {
        path: '/blog/course-:courseId/',
        isExact: true,
        components: {
            main: CoursePage,
            topSection: SectionTopCourse,
            panel: CoursePanel
        }
    },
    {
        path: '/blog/course-:courseId/lesson-:lessonId/',
        isExact: true,
        components: {
            main: LessonPage,
            topSection: SectionTopLesson,
            panel: CoursePanel
        }
    },
    {
        path: '*',
        components: {
            main: ErrorPage,
            topSection: null,
            panel: null
        }
    }
];
