import React from "react";
import {Route, Switch} from "react-router-dom";
import {path} from "./path";
import {withCourseAccess} from "../hoc/withCourseAccess";

const MainRoutes = () => {
    return <Switch>
        {
            path.map((route, key) => {
                return <Route
                    key={ key }
                    exact={ !!route.isExact }
                    path={ route.path }
                    component={ withCourseAccess(route.components.main) }
                />
            })
        }
    </Switch>
};

export default MainRoutes;
