import * as actionTypes from "../actionTypes";

export const mainPageActions = {
    /**
     * Сохранение списка названий основных разделов приложения.
     * Разделы отображаются в плавающей панели на главной странице.
     * @param params
     * @returns {{type: string, params}}
     */
    setAppSections: params => ({
        type: actionTypes.SET_APP_SECTIONS,
        params
    }),

    /**
     * Обновление активного раздела приложения.
     * Срабатывает по клику на верхнее меню либо при скролле главной страницы
     * @param params
     * @returns {{type: string, params}}
     */
    updateAppSection: params => ({
        type: actionTypes.UPDATE_ACTIVE_CATEGORY,
        params
    }),

    /**
     * Сохранение полученного от сервера списка ключей
     * для сложности курсов
     */
    setDifficultyTypes: params => ({
       type: actionTypes.SET_DIFFICULTY_TYPES,
       params
    }),

    /**
     * Сохранение полученного от сервера списка курсов, сервисов и вебинаров
     * @param params
     * @returns {{type: string, params}}
     */
    setCoursesList: params => ({
        type: actionTypes.SET_COURSES_LIST,
        params
    }),

    /**
     * Сохранение списка ID купленных курсов
     * @param params
     * @returns {{type: string, params}}
     */
    setPayedCoursesList: params => ({
        type: actionTypes.SET_PAYED_COURSES_LIST,
        params
    }),

    /**
     * Сохранение полученного от сервера списка фильтров для курсов, сервисов и вебинаров
     * @param params
     * @returns {{type: string, params}}
     */
    setFilter: params => ({
        type: actionTypes.SET_FILTER,
        params
    }),

    /**
     * Обновление фильтров для курсов, сервисов и вебинаров.
     * Происходит по клику на кнопки фильтра в соответствующем компоненте
     * @param params
     * @returns {{type: string, params}}
     */
    updateFilter: params => ({
        type: actionTypes.UPDATE_FILTER,
        params
    }),

    /**
     * Сохранение полученных от сервера слайдов для слайдера в верхней секции
     * на главной странице
     * @param params
     * @returns {{type: string, params}}
     */
    setTopSectionSlides: params => ({
        type: actionTypes.SET_TOP_SECTION_SLIDES,
        params
    })
}
