import React from 'react';
import {statusType} from "../../../types";
import {NavLink} from "react-router-dom";

import './lessonsList.sass';
import LessonContent from "./lessonContent/lessonContent";

const LessonsList = ({ course }) => {
    const lessons = course.lessons;

    const getIconClass = (status) => {
        switch (status) {
            case statusType.finished:
                return 'icon-check';

            case statusType.started:
                return 'icon-pause';

            case statusType.enabled:
                return 'icon-play';

            case statusType.disabled:
                return 'icon-lock';

            default: return 'icon-check';
        }
    };

    return <div className="lessonsList">
        <h2 className="titleBordered">Занятия семинара</h2>

        { lessons && lessons.length > 0 ?
            <ul className="lessonsList__list">
                {lessons.map((lesson, index) => {
                    return <li key={index}>
                        {lesson.status === statusType.disabled ?
                            <span
                                key={lesson.id}
                                className={`element ${lesson.status === statusType.disabled ? 'disabled' : null}`}
                            >
                                <LessonContent
                                    lesson={lesson}
                                    index={index}
                                    icon={getIconClass(lesson.status)}
                                />
                            </span>
                            :
                            <NavLink
                                to={`/blog/course-${course.id}/lesson-${lesson.id}/`}
                                key={lesson.id}
                                className={`element ${lesson.status === statusType.disabled ? 'disabled' : null}`}
                            >
                                <LessonContent
                                    lesson={lesson}
                                    index={index}
                                    icon={getIconClass(lesson.status)}
                                />
                            </NavLink>
                        }
                    </li>
                })}
            </ul>
            :
            <p>Уроки не найдены :(</p>
        }
    </div>
};

export default LessonsList;
