import React from "react";
import {Route, Switch} from "react-router-dom";
import {path} from "./path";

const TopSectionRoutes = () => {
    return <Switch>
        {
            path.map((route, key) => {
                return <Route
                    key={ key }
                    exact={ !!route.isExact }
                    path={ route.path }
                    component={ route.components.topSection }
                />
            })
        }
    </Switch>
};

export default TopSectionRoutes;
