import React from 'react';
import {buttonMoreType} from "../../../types";

const ButtonMore = ({ type, link, handler, subclass, children }) => {
    const handleClick = (e) => {
        handler(e);
    }

    const getContent = () => {
        return <>
            <span className="icon icon-arr_rounded_right"/>
            <span className="text">{ children }</span>
        </>;
    }

    return type === buttonMoreType.link ?
        <a href={ link } className={`more ${subclass}`}>{ getContent() }</a>
        :
        <button onClick={ handleClick } className={`more ${subclass}`}>{ getContent() }</button>;
};

export default ButtonMore;
