import * as axios from "axios";

const apiVersion = 'v1';

const instance = axios.create({
    baseURL: `https://fin-plan.org/api/learning/${apiVersion}/`
});
const instanceUser = axios.create({
    baseURL: `https://fin-plan.org/api/techanalysis/`
});

export const userAPI = {
    /**
     * GET запрос на получение данных текущего пользователя
     */
    getUserData() {
        return instanceUser
            //.get(`/getUser/work/`)
            .get(`/getUser/debug/userId:1/authorized:true/isAdmin:true/payRadar:false/`)
            // .get(`/getUser/debug/userId:7307/authorized:true/isAdmin:true/payRadar:false/`)
            .then(response => response.data);
    }
};

export const mainPageAPI = {
    /**
     * GET запрос на получение списка тэгов (категории и разделы для фильтра)
     * @returns {*}
     */
    getTagsLibrary() {
        return instance
            .get(`getTagsLibrary/`)
            .then(response => response.data);
    },

    /**
     * GET запрос на получение полного списка курсов, сервисов и вебинаров
     * @returns {*}
     */
    getCoursesList() {
        return instance
            .get(`getCoursesList/full/full`)
            .then(response => response.data);
    },

    /**
     * GET запрос на получения списка курсов, которые купил юзер.
     * Обязательно указывать только id пользователя.
     * @param userId
     * @param courseId - необязательный параметр
     * @returns {*}
     */
    getPurchasedCoursesList(userId, courseId = null) {
        return instance
            .get(`getPayedItems/${userId}` + (courseId ? `/${courseId}` : ''))
            .then(response => response.data);
    }
};

export const coursePageAPI = {
    /**
     * GET запрос на получение списка уроков для курса
     *
     * @param id - идентификатор курсе, для которого запрашиваем список уроков
     * @returns {*}
     */
    getCourseData(id) {
        return instance
            .get(`getCourseDetail/${id}/y`)
            .then(response => response.data);
    },

    /**
     * GET запрос на получение статуса курсов.
     * Обязательный параметр только userId.
     *
     * Для получения статистики только по
     * определенному курсу нужно указать courseId
     *
     * @param userId
     * @param courseId
     * @returns {*}
     */
    getCourseStatistics(userId, courseId = null) {
        return instance
            .get(`getLearningStatistics/${userId}` + (courseId ? `/${courseId}` : ''))
            .then(response => response.data);
    },

    /**
     * GET запрос на получение данных урока по его id
     *
     * @param userId
     * @param lessonId
     * @returns {*}
     */
    getLessonData(userId, lessonId) {
        return instance
            .get(`getLessonElement/full/${lessonId}/${userId}`)
            .then(response => response.data);
    },

    /**
     * POST запрос на сохранение текущего времени
     * просмотра видео
     *
     * @param userId
     * @param lessonId
     * @param videoId
     * @param time - время в формате hh:mm:ss (00:15:35)
     * @param isFinished - полностью просмотрено видео или нет
     * @returns {*}
     */
    setVideoProgress(userId, lessonId, videoId, time, isFinished = false) {
        return instance
            .post(`setVideoStatistics/${userId}/${lessonId}/${videoId}/${time}/${isFinished ? 'y' : 'n'}`);
    },

    /**
     * POST запрос на обновление статуса урока
     *
     * @param userId
     * @param courseId
     * @param updateDate
     * @param lessonId
     * @param lessonStatus
     * @returns {*}
     */
    updateLessonStatus({userId, courseId, updateDate, lessonId, lessonStatus}) {
        return instance
            .put('setLearningStatistics/', {
                data: {
                    uid: userId,
                    courseId,
                    lastViewCourseDate: updateDate,
                    lessonId,
                    lessonStatus
                }
            },
            {
                headers: {
                    'Content-Type': 'text/plain'
                }
            });
    },

    /**
     * POST запрос на завершение курса
     *
     * @param userId
     * @param courseId
     * @param updateDate
     * @returns {*}
     */
    finishCourse({userId, courseId, updateDate}) {
        return instance
            .put('setLearningStatistics/', {
                data: {
                    uid: userId,
                    courseId,
                    lastViewCourseDate: updateDate,
                    courseSuccessEnd: true
                }
            },
            {
                headers: {
                    'Content-Type': 'text/plain'
                }
            });
    },

    /**
     * POST запрос на обновление статуса теста
     *
     * @param userId
     * @param courseId
     * @param updateDate
     * @param testId
     * @param testStatus
     * @returns {*}
     */
    updateTestStatus({userId, courseId, updateDate, testId, testStatus}) {
        return instance
            .put('setLearningStatistics/', {
                data: {
                    uid: userId,
                    courseId,
                    lastViewCourseDate: updateDate,
                    testId,
                    testStatus
                }
            },
            {
                headers: {
                    'Content-Type': 'text/plain'
                }
            });
    },

    /**
     * GET запрос на получение списка доступных типов вопросов с их id от сервера
     *
     * @returns {*}
     */
    getTestQuestionTypes() {
        return instance
            .get('getQuestionTypes/')
            .then(response => response.data);
    },

    /**
     * POST запрос на сохранение на сервера варианта ответа юзера на вопрос теста
     *
     * @param uid
     * @param testId
     * @param questionId
     * @param questionType
     * @param answerId
     * @returns {*}
     */
    updateTestAnswer({uid, testId, questionId, questionType, answerId}) {
        return instance
            .post(`setUserTestAnswer/${uid}/${testId}/${questionId}/${questionType}/${answerId}`);
    },

    /**
     * POST запрос на удаление на сервере варианта ответа юзера на вопрос теста
     *
     * @param uid
     * @param testId
     * @param questionId
     * @param answerId
     * @returns {*}
     */
    deleteTestAnswer({uid, testId, questionId, answerId}) {
        return instance
            .post(`clearUserTestAnswer/${uid}/${testId}/${questionId}/${answerId}/`);
    },

    /**
     * POST запрос на удаление всех вариантов ответа юзера на вопросы теста
     *
     * @param uid
     * @param testId
     * @returns {*}
     */
    deleteAllTestAnswers({uid, testId}) {
        console.log(testId)
        return instance
            .post(`clearAllUserTestAnswers/${uid}/${testId}/`)
    }
};
